import LookDetailController from './look-detail-controller'
import { select, selectAll, getCollection } from 'lib/selectors'
import {focusElement} from 'lib/helpers'
import * as dBind from 'delegate'

export default function LookDetail(el) {
  let controller
  let lookAdditionalImages
  const NEXT_LOOK_SELECTOR = '.js-link-next-look'
  const PREV_LOOK_SELECTOR = '.js-link-prev-look'
  const EXPLORE_LOOK = '.js-explore-look'
  const PAGE_CONTROL_BULLETS = '.js-page-control'
  const BACK_TO_LOOKS = '.nav-left'
  const PAGE_TYPE = dataLayerGA.pageLoad.page_type

  window.addEventListener('load', init)

  function init() {
    controller = new LookDetailController(el.querySelector('.look-page__container'))
    bind()
  }

  function bind() {
    controller.enter()
    const bulletsEl = selectAll('.js-page-control li', el)
    let observer = new MutationObserver(mutationCallback)
    bulletsEl.forEach((el) => {
    	observer.observe(el, {attributes : true})
    })
    const exploreLook = select(EXPLORE_LOOK)
    if (exploreLook) {
    	exploreLook.addEventListener('click', onExploreThisLook)
    }   
  }
  
  function mutationCallback(mutationList) {
	  for (let mutation of mutationList) {
		  if (mutation.type == "attributes" && mutation.attributeName == "class") {
		     let currentClassList = mutation.target.classList.contains('is-active')
		     if (currentClassList) {
		        let dlBulletDetail = JSON.parse(mutation.target.parentElement.dataset.datalayer)
		        dlBulletDetail.eventLabel = mutation.target.dataset.number
		        dlBulletDetail.eventCategory = PAGE_TYPE
		        window.document.DLKit.push('on-tracking', dlBulletDetail)
		     }
		  }
	  }
  }
  
  function onExploreThisLook(){
	  window.document.DLKit.push('when-user-interact-with-pdp-ready-to-wear', {
	      eventLabel: '',
	      eventAction :'explore this look'
	    })
  }
}
















