import popin from 'modules/popin'
import {select, getCollection, closest} from 'lib/selectors'
import * as xhr from 'modules/fetch'
import config from 'lib/config'
import { debounce, throttle } from 'lodash'
import { Manager, WINDOW_WAS_RESIZED, BREAKPOINT_CHANGE } from 'lib/events'
import disclaimer from 'ui/footer/disclaimer'
import { isMobile, isMobileScreen, isTabletScreen, isWideScreen, focusElement } from 'lib/helpers'

const MODAL_OPENER = '.js-view-detail-modal'

class ViewDetailsModal {
	constructor(el) {
		this.el = el
		this.popinCol = null
		this.buttonOpener = null
		this.data = null
		this.mainImgData = null
		this.referenceProducts = null
		this.imageData = null
		this.lookDetails = null
		this.lookCategory = null
		this.expandDetailsBtn = null
		this.lookProductsList = null
		this.showDisclaimerText = false
		this.openedProductName = null
		this.pageControlDots = null
		this.productWrapperDiv = null
		this.pageControlWrapper = null
		this.lookImageContainer = null
		this.navPageControl = null
		this.productDetailsContainer = null
		this.productListContent = null
		this.productSectionWrapper = null
		this.lookMainImg = null
		this.imageZoomContent = null
		this.lookItems = null
		this.lookItemsMinMax = null
		this.productSectionClicked = false
		this.scrollPosition = null
		this.controlDotsClicked = false
		this.modalBackground = null
		this.mobileScrollTimer = null
		this.initialX = 0
	    this.initialY = 0
	    this.didMove = false
		this.currentOpenProduct = null
		this.currentTransformValue = 0
		this.transformPercent = 0
	    this.deltaX = 0
	    this.deltaY = 0
	    this.frameHeight = 0
	    this.transformY = 0
	    this.triggeredMention = null
	    this.activeTitle = null
		this.activeDesc = null
		this.activeref = null
		this.listCollapseHeader = null
		this.slidingElement = null
		this.previousYTransform = null
		this.initialScrollTop = null
		this.lastProductDelta = 60
	}

	async init() {
		Manager.unsubscribe(BREAKPOINT_CHANGE)
		this.buttonOpener = select(MODAL_OPENER)
		this.packshotDefault = select('*#look-details-0 img')
		this.lookDetails = JSON.parse(this.buttonOpener.dataset.code)
		this.lookCategory = this.lookDetails.lookCategory
		this.viewDetailsClicked()
		this.data = await xhr.get(config.popinLookPDP.lookDataUrl.replace('{0}',this.lookDetails.lookCode))
		if (this.data && this.data !== null) {
		  this.mainImgDataUrl = this.packshotDefault ? this.packshotDefault.src : ''
		  this.referenceProducts = this.data.referenceProducts
		  this.referenceProducts.forEach((item) => {
			  if (item.geoAsset !== null && item.geoAsset.positions !== undefined && item.geoAsset.positions.length !== 0) {
				  item.calculatedGeo = this.minYPosition(item.geoAsset)
			  }
		  })
		  this.referenceProducts.sort((a,b) => {
			  if (a.geoAsset !== null && b.geoAsset !== null) {
				  if (a.calculatedGeo < b.calculatedGeo) {
					  return -1
				  } else {
					  return 1
				  }
				  return 0
			  } else if (a.zoomAnimationData !== null && b.zoomAnimationData !== null) {
				  let minA = parseFloat(a.zoomAnimationData.min) + (parseFloat(a.zoomAnimationData.max) - parseFloat(a.zoomAnimationData.min)) / 2
				  let minB = parseFloat(b.zoomAnimationData.min) + (parseFloat(b.zoomAnimationData.max) - parseFloat(b.zoomAnimationData.min)) / 2
				  if (minA < minB) {
					  return -1
				  } else {
					  return 1
				  }
				  return 0
			  }
		  })
		  this.imageAltText = this.data.lookImage.altTextLocalized !== null ? this.data.lookImage.altTextLocalized : ''
		  this.buildTemplate()
		}
	}

	minYPosition(asset) {
		if (asset.positions.length > 0) {
			let minY = asset.positions[0].y
			let maxY = asset.positions[0].y
			let returnValue
			asset.positions.forEach((pos) => {
				maxY = pos.y > maxY ? pos.y : maxY
				minY = pos.y < minY ? pos.y : minY
			})
			returnValue = Math.abs(minY - 50) + (Math.abs(maxY - 50) - Math.abs(minY - 50)) / 2
			return returnValue
		}
		return false
	}

	open() {
	  popin.open({
	    html: this.template,
	    role: 'dialog',
	    modifiers: 'is-relative look-popin',
	    opener: null,
	    modal: true,
	    label: config.popinLookPDP.modalLabel,
	    forceInert: true,
	    manageFocus: true,
	    outerClose: true
	  }).then(() => {
	    this.onOpenPopin()
	  })
	}

	viewDetailsClicked() {
	  window.document.DLKit.push('when-user-interact-with-product-page', {
		eventLabel : "",
		eventAction :"view details"
	  })
	}

	buildTemplate() {
	  this.template =
		  `
		  <div class="popin-text text-center look-popin__content" data-close-label="${window.config.popinLookPDP.closeLabel}">
		    <div class="look-popin__heading">
		      <div class="look-popin-details__container look-popin-image__container">
		        <div class="look-popin-zoom__wrapper">
		          <div class="look-popin-zoom__content">
		            <div class="look-popin-zoom__image">
		              <img alt="`+ this.imageAltText +`" src="`+ this.mainImgDataUrl +`" class="look-popin__mainImg" />
		            </div>
		          </div>
		        </div>
		      </div>`
		      + this.mobileNavDotTemplate() +
		      `<div class="look-popin-details__list">
		        <div class="look-popin-list__header is-hidden-l">
		          <p class="heading" role="heading" aria-level="1">
		            <span class="collapse-product-panel js-collapse-list" role="button" tabindex="0" aria-expanded="false">
		              <span class="is-sr-only list-title">${window.config.popinLookPDP.modalLabel}</span>
		              <span class="is-sr-only"> – `+ this.lookDetails.lookCategory +`</span>
		              <span class="down-icon-container">
		                <svg class="icon is-large is-down-mediumThin" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
		                  <use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg#down-mediumThin"/>
		                </svg>
		              </span>
		            </span>
		          </p>
		        </div>
		        <div class="look-popin-details__container look-popin-products__container">
		          <div class="look-popin-product__position center-aligned">
		            <div class="look-popin-product__wrapper">
		              <div class="look-popin-product__content">
		                <p role="heading" aria-level="1" class="is-sr-only">
		                  <span>
		                    <span>${window.config.popinLookPDP.modalLabel}</span>
		                    <span> – `+ this.lookDetails.lookCategory +`</span>
		                  </span>
		                </p>`
		              + this.productListTemplate() +
		            `</div>`
		            + this.disclaimerTemplate() +
		          `</div>
		          </div>
		      </div>
		    </div>
		  </div>
		  </div>
		  `
	}

	mobileNavDotTemplate() {
		let html = ''
		if (this.referenceProducts.length > 0) {
		  html = `<div class="look-popin__controls">
			<nav role="navigation" aria-label="${window.config.popinLookPDP.modalLabel} - `+ this.lookCategory +`">
		    <ul class="page-control js-page-control">`
		    for (let product = 0; product < this.referenceProducts.length; product++) {
			  html = html +
			    `<li data-number="`+ product +`">
			      <button type="button" class="look-page__control-btn js-product-navigation" data-sectionId="look-details-card_`+ this.referenceProducts[product].code +`">
			        <span class="is-sr-only">`
			         + this.referenceProducts[product].name +
			        ` - ${window.config.popinLookPDP.modalLabel}</span>
			      </button>
				</li>`
		    }
		  html = html + '</ul></nav></div>'
		}
		return html
	}

	disclaimerTemplate() {
		let disclaimerText = ''
		if (this.showDisclaimerText) {
			disclaimerText = disclaimerText +  `
			<div id="disclaimer-text" class="disclaimer js-disclaimer disclaimer-display">
			  <div class="disclaimer-container">
		        <p id="disclaimer-back-content" tabindex="-1">
		          <a id="js-disclaimer-back-to-target" class="disclaimer__back" aria-label="${window.config.popinLookPDP.disclaimerAlly}" href="#">↩</a>
	            </p>
	            <p class="content">
	              ${window.config.popinLookPDP.disclaimerText1}
	             <a target="_blank" class="link is-underline is-gray is-paddingLeft"
			       title="${window.config.popinLookPDP.disclaimerTitle}" rel="nofollow"
			       href="${window.config.popinLookPDP.policyURL}">
			       <span>${window.config.popinLookPDP.disclaimerText2}</span>
		         </a>
	           </p>
		     </div>
		   </div>
		   `
		}
		return disclaimerText
	}

	productListTemplate() {
	  let html = ''
	  if (this.referenceProducts.length > 0) {
		html = `<div class="look-detail-list__content">
		<ul class="look-detail-product__list">`
	    for (let product = 0; product < this.referenceProducts.length; product++) {
		  html = html + `<li class="look-detail-product__item" id="look-details-card_`+ this.referenceProducts[product].code +`">
		    <div class="look-detail-product__content" data-max="0" data-min="0">
		      <p class="heading look-detail-product__title" role="heading" aria-level="2" tabindex="-1">
		        <span class="look-detail-product__expand js-expand-look-details" id="look-product-`+ product +`" role="button" tabindex="0" aria-expanded=`+false+`>
		     	  `+ this.referenceProducts[product].name +`
		     	  <span class="icon-wrapper">
		     	    <svg class="icon is-small is-next" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
		              <use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg#next"/>
		            </svg>
		     	  </span>
		     	</span>
		      </p>
		     <div class="look-detail-product__details">
		         ` + this.productDescriptionText(this.referenceProducts[product]) + `
		         ` + this.productRefText(this.referenceProducts[product]) + `
		         ` + this.productPriceText(this.referenceProducts[product], product) + `
		         ` + this.detailBtnTemplate(this.referenceProducts[product]) + `
		     </div>
		   </div>
		 </li>`
	    }
		html = html + '</ul></div>'
	  }
	   return html
	}

	productDescriptionText(refProduct) {
		if (refProduct.fabricGrp !== null && refProduct.colorGrp !== null) {
		  if (refProduct.fabricGrp.name !== '' && refProduct.colorGrp.name !=='') {
			return '<p class="look-detail-product__description">' + refProduct.fabricGrp.name + '<br>' + refProduct.colorGrp.name + '</p>'
		  } else if (refProduct.fabricGrp.name === '' && refProduct.colorGrp.name !=='') {
			return '<p class="look-detail-product__description">' + refProduct.colorGrp.name + '</p>'
		  } else if (refProduct.fabricGrp.name !== '' && refProduct.colorGrp.name ==='') {
			return '<p class="look-detail-product__description">' + refProduct.fabricGrp.name + '</p>'
		  }
		} else if ((refProduct.fabricGrp === null && refProduct.colorGrp !== null) || (refProduct.fabricGrp !== null && refProduct.colorGrp === null)) {
			let productDescription = refProduct.fabricGrp === null ? refProduct.colorGrp : refProduct.fabricGrp
			if (productDescription.name !== '') {
				return '<p class="look-detail-product__description">' + productDescription.name + '</p>'
			}
		}
		return ''
	}

	productRefText(refProduct) {
	  if (refProduct.manufacturerAID != '') {
		return '<p class="look-detail-product__ref">' + refProduct.manufacturerAID + ' ' + refProduct.refFshCode + '</p>'
      } else {
		return '<p class="look-detail-product__ref">' + refProduct.refFshCode + '</p>'
	  }
	}

	productPriceText(refProduct, index) {
		if(refProduct.priceMention) {
			let priceText =  (refProduct.price !== null && refProduct.displayPrice && !refProduct.isPriceUnderRequest) ? refProduct.price.formattedValue : ''
			if (priceText !== '') {
				if (!refProduct.sellableOnline && priceText !== '') {
					this.showDisclaimerText = true
		      		priceText = priceText + '<a class="disclaimer-indicator look-detail-product__asterisk" data-test="lblDisclaimerIndicator" href="#disclaimer-text" aria-label="' + config.popinLookPDP.priceInfoAriaLabel + '" data-index="'+ index +'">*</a>'
				}
				return '<p class="heading look-detail-product__price">' + priceText + '</p>'
			} else {
				return '<p class="look-detail-product__price">' + config.popinLookPDP.priceOnRequest + '</p>'
			}
		}
		return ''
	}

	detailBtnTemplate(refProduct) {
		const currentLocale = config.currentLocale.replace(/\//g, '')
		if ((refProduct.nonWebSelection !== '' && !refProduct.nonWebSelection) && (refProduct.nonCommunication !== '' && !refProduct.nonCommunication) && (refProduct.availableToCurrentUser == '' || (refProduct.availableToCurrentUser !== '' && refProduct.availableToCurrentUser))) {
			return `
			  <p class="product-description__cta">
			    <a href=`+ '/' + currentLocale + refProduct.url +` class="link has-icon is-right is-gray js-view-details" aria-label ="`+ config.popinLookPDP.viewDetailLabel.replace('{0}',refProduct.name) +`">
			      <span>
			        <span>${window.config.popinLookPDP.viewProductDetailsCta}</span>
			        <svg class="icon is-small is-next" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
                      <use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg#next"/>
                    </svg>
			      </span>
			    </a>
			  </p>
			`
		}
		return ''
	}

	onOpenPopin() {
		this.setDomElement()
		this.setInitialTemplate()
	    if (select('.disclaimer-indicator')) {
	    	disclaimer()
	    }
		this.setPositionValue()
		this.lookItemsMinMax = this.lookItems.length > 0 ? Array.from(this.lookItems).map((element) => { return { min: parseFloat(element.dataset.min), max: parseFloat(element.dataset.max) } }) : []
		Manager.subscribe(BREAKPOINT_CHANGE, this.setInitialTemplate.bind(this))
	}

	setDomElement() {
	  this.popinEl = select('.look-popin')
	  this.popinCol = select('.popin__col', this.popinEl)
	  this.buttonClose = select('button.popin__close', this.popinEl)
	  this.expandDetailsBtn = getCollection('.js-expand-look-details')
	  this.lookProductsList = getCollection('.look-detail-product__item')
	  this.pageControlWrapper = select('.look-popin__controls', this.popinEl)
	  this.buttonClose ? this.buttonClose.addEventListener('click', this.closeLookPopin.bind(this)) : ''
	  this.viewDetailsCta = getCollection('.js-view-details', this.popinEl)
	  this.backToTarget = select('#js-disclaimer-back-to-target', this.popinEl)
	  this.productWrapperDiv = select('.look-popin-product__position', this.popinEl)
	  this.productDetailsContainer = select('.look-popin-products__container', this.popinEl)
	  this.productSectionWrapper = select('.look-popin-product__content', this.popinEl)
	  this.productListContent = select('.look-detail-list__content', this.popinEl)
	  this.imageZoomContent = select('.look-popin-zoom__content', this.popinEl)
	  this.pageControlList = select('.js-page-control', this.popinEl)
	  this.navPageControl = select('.look-popin__controls nav', this.popinEl)
	  this.pageControlDots = getCollection('.js-page-control li', this.popinEl)
	  this.lookMainImg = select('.look-popin-zoom__image img', this.popinEl)
	  this.lookItems = getCollection('.look-detail-product__content', this.popinEl)
	  this.modalBackground = select('.popin__background')
	  this.modalBackground ? this.modalBackground.parentNode.removeChild(this.modalBackground) : ''
	  if (this.backToTarget) {
		this.backToTarget.addEventListener('click', this.focusToTarget.bind(this))
	  }
	  this.viewDetailsCta.forEach((cta) => {
		cta.addEventListener('click', this.viewProductDetails.bind(this))
	  })
	  this.lookProductsList.forEach((product) => {
		if (select('.product-description__cta', product) !== null) {
		  select('.look-detail-product__ref', product).classList.add('is-sr-only')
		}
	  })
	}

	setInitialTemplate() {
	  if ((!isMobileScreen() && !isTabletScreen())) {
		this.transformProductSection()
		this.expandFirstProduct()
		let timer = null
		this.popinCol.addEventListener('scroll',() => {
		  if(timer !== null) {
		    clearTimeout(timer)
		  }
		  timer = setTimeout(() => {
			if (!this.productSectionClicked) {
			  this.onImageScroll()
			} else {
				this.productSectionClicked = false
			}
		  }, 250)
		}, false)
		this.expandDetailsBtn.forEach((elem) => {
		  elem.addEventListener('click', this.expandClickedProduct.bind(this))
		  elem.addEventListener('keyup', this.expandClickedProduct.bind(this))
		})
	  } else {
		this.transformMobileProductCard()
	  }
	}

	setPositionValue() {
		for (let prod = 0; prod < this.referenceProducts.length; prod++) {
			const positionData = this.referenceProducts[prod].geoAsset ? this.referenceProducts[prod].geoAsset.positions : ''
			const referenceProduct = select('#look-details-card_'+this.referenceProducts[prod].code+' div')
			const maxScrollTop = this.lookMainImg.offsetHeight - window.innerHeight
			let minY = 0
			let maxY = 0
			if (positionData && positionData.length > 0) {
				const normalizeCoef = 50
				minY = positionData[0].y
				maxY = positionData[0].y
				positionData.forEach(pos => {
					maxY = pos.y > maxY ? pos.y : maxY
					minY = pos.y < minY ? pos.y : minY
				})
				referenceProduct.setAttribute('data-max',Math.abs(parseFloat(minY) - normalizeCoef))
				referenceProduct.setAttribute('data-min',Math.abs(parseFloat(maxY) - normalizeCoef))
			} else if (this.referenceProducts[prod].zoomAnimationData !== null) {
				minY = this.referenceProducts[prod].zoomAnimationData.min
				maxY = this.referenceProducts[prod].zoomAnimationData.max
				referenceProduct.setAttribute('data-max',parseFloat(maxY))
				referenceProduct.setAttribute('data-min',parseFloat(minY))
			}
		}
	}

	focusToTarget() {
	  const expandedProductDetails = select('.look-detail-product__details--enabled')
	  const el = isMobile() ? this.triggeredMention : select('.disclaimer-indicator', expandedProductDetails)
	  if (el) {
        focusElement(el)
      }
	}

	viewProductDetails(evt) {
		window.document.DLKit.push('on-tracking', {
	      event: 'uaevent',
	      eventAction: 'view details',
          eventLabel: this.openedProductName,
	      eventCategory: 'pdp_look_popin'
	    })
	}

	transformProductSection() {
		if (this.productSectionWrapper) {
			const mobileIconWrapper = select('.mobile-icon-wrapper', this.productSectionWrapper)
			if (mobileIconWrapper !== null && mobileIconWrapper !== "") {
				mobileIconWrapper.classList.remove('mobile-icon-wrapper')
				mobileIconWrapper.classList.add('is-sr-only')
				if (Array.from(mobileIconWrapper.children).length > 0) {
				  	const mobileIconSpan = Array.from(mobileIconWrapper.children)[0]
				  	mobileIconSpan.removeAttribute('role')
					mobileIconSpan.removeAttribute('tabindex')
					mobileIconSpan.removeAttribute('aria-expanded')
					Array.from(mobileIconSpan.children).forEach(elem => elem.classList.remove('is-sr-only'))
				}
			}
		}
		if (this.lookProductsList.length > 0) {
			this.lookProductsList.forEach(elem => {
				let titleSpan = select('.look-detail-product__expand', elem)
				titleSpan.setAttribute('role','button')
				titleSpan.setAttribute('tabindex','0')
				titleSpan.setAttribute('aria-expanded','false')
			})
		}
	}

	expandFirstProduct() {
	  if (this.lookProductsList.length > 0) {
		const productDetailsSection = select('.look-detail-product__details',this.lookProductsList[0])
		const productSectionToExpand = select('.look-detail-product__expand',this.lookProductsList[0])
		this.openedProductName = productSectionToExpand.innerText ? productSectionToExpand.innerText : ''
		productDetailsSection.classList.add('look-detail-product__details--enabled')
		select('.look-detail-product__title',this.lookProductsList[0]).classList.add('look-detail-product__title--active')
		TweenLite.to(productDetailsSection, 0.2, {
			display: 'block',
			overflowY: 'visible',
			height:'auto',
			paddingTop: '5px',
			onComplete: () => {
			  if (this.productWrapperDiv !== '' && this.productWrapperDiv !== null) {
				  const productWrapperOverflow = this.productWrapperDiv.offsetHeight > window.innerHeight ? true : false
				  if (productWrapperOverflow) {
				    TweenLite.to(this.productWrapperDiv, 0.1, {
					    css : {
						    height: '100%'
					    }
				    })
				  }
			  }
			}
		})
		productSectionToExpand.setAttribute('aria-expanded', true)
		productSectionToExpand.setAttribute('aria-disabled', true)
		select('.icon-wrapper',this.lookProductsList[0]).style.display = "none"
	  }
	}

	transformMobileProductCard() {
		const priceMention = getCollection('.disclaimer-indicator')
		const BORDER_GAP = 10
		const paddingTop = 27
		if (this.lookProductsList.length > 0) {
			let productWrapperHeight = this.productDetailsContainer.offsetHeight
			this.lookProductsList.forEach((elem,index) => {
				let titleSpan = select('.look-detail-product__expand', elem)
				if (index == 0) {
					this.transformPercent = (productWrapperHeight - elem.offsetHeight) / productWrapperHeight* 100
					elem.classList.add('is-active')
					this.pageControlList.style.height = (window.innerHeight - elem.offsetHeight + BORDER_GAP) + 'px'
					this.imageZoomContent.style.height = (window.innerHeight - elem.offsetHeight + BORDER_GAP) + 'px'
					TweenLite.set(this.productDetailsContainer, { y: `${(productWrapperHeight - elem.offsetHeight) / productWrapperHeight* 100}%`})
					TweenLite.set(this.productListContent, { y: 0})
				}
				titleSpan.setAttribute('role','presentation')
				titleSpan.removeAttribute('tabindex')
				titleSpan.removeAttribute('aria-expanded')
				titleSpan.removeAttribute('aria-disabled')
			})
		}
		if (this.productSectionWrapper) {
			Array.from(this.productSectionWrapper.children).forEach((elem) => {
				if (elem.classList.contains('is-sr-only')) {
					elem.classList.remove('is-sr-only')
					elem.classList.add('mobile-icon-wrapper')
					if (Array.from(elem.children).length > 0) {
						const mobileIconSpan = Array.from(elem.children)[0]
						mobileIconSpan.setAttribute('role','button')
						mobileIconSpan.setAttribute('tabindex','0')
						mobileIconSpan.setAttribute('aria-expanded','false')
						Array.from(mobileIconSpan.children).forEach(elem => elem.classList.add('is-sr-only'))
					}
				}
			})
		}
		this.imageZoomContent.addEventListener('mousedown', this.handleImageStartSliding.bind(this))
	    this.imageZoomContent.addEventListener('touchstart', this.handleImageStartSliding.bind(this))
	    this.currentOpenProduct = select('.look-detail-product__item.is-active')
		this.activeTitle = select('.is-active .look-detail-product__title', this.popinEl)
	    this.activeDesc = select('.is-active .look-detail-product__description', this.popinEl)
	    this.activeref = select('.is-active .look-detail-product__ref', this.popinEl)
		this.listCollapseHeader = select('.look-popin-list__header')
		const mobileIconWrapper = select('.mobile-icon-wrapper', this.productSectionWrapper)
		const collapseIcon = select('.js-collapse-list')
		mobileIconWrapper.style.height = this.activeTitle.offsetHeight + this.activeref.offsetHeight + (this.activeDesc !== null ? this.activeDesc.offsetHeight : 0) + paddingTop + 'px'
		mobileIconWrapper.addEventListener('click', this.showProductDetails.bind(this))
		collapseIcon.addEventListener('click', this.collapseListPanel.bind(this))
		let that = this
		mobileIconWrapper.addEventListener('keyup', (event) => {
			if (event.which == 32 ||  event.which == 13) that.showProductDetails()
		})
		collapseIcon.addEventListener('keyup', (event) => {
			if (event.which == 32 ||  event.which == 13) that.collapseListPanel()
		})
		mobileIconWrapper.addEventListener('touchstart', this.handleSlidingStart.bind(this))
        mobileIconWrapper.addEventListener('mousedown', this.handleSlidingStart.bind(this))
		this.zoomContentHeight = this.imageZoomContent.offsetHeight
		if (priceMention.length > 0) {
			priceMention.forEach((elem) => {
				elem.addEventListener('click', () => {
					this.triggeredMention = this.lookProductsList[elem.dataset.index]
				})
			})
		}
		if (this.pageControlDots.length > 0) {
		  this.pageControlDots.forEach((elem, index) => {
			let pageControlBtn = select('.look-page__control-btn', elem)
			if (index == 0) {
			  elem.classList.add('is-active')
			  pageControlBtn ? pageControlBtn.setAttribute('aria-current', true) : ''
			}
			pageControlBtn.addEventListener('click', this.scrollToProduct.bind(this))
		 })
	    }
	}

	handleImageStartSliding(e) {
		if (!e.touches) {
		    e.preventDefault()
		  }
	    this.initialX = e.touches ? e.touches[0].clientX : e.clientX
	    this.initialY = e.touches ? e.touches[0].clientY : e.clientY
	    this.deltaY = 0
	    this.didMove = false
	    this.imageZoomContent.addEventListener('mouseup', this.handleImageEndSliding.bind(this))
	    this.imageZoomContent.addEventListener('mouseleave', this.handleImageEndSliding.bind(this))
	    this.imageZoomContent.addEventListener('mousemove', this.handleImageSliding.bind(this))
	    this.imageZoomContent.addEventListener('touchmove', this.handleImageSliding.bind(this))
	    this.imageZoomContent.addEventListener('touchend', this.handleImageEndSliding.bind(this))
	}

	handleImageSliding(e) {
		if (e.touches) {
		    this.deltaY = this.initialY - e.touches[0].clientY
		  } else {
		    this.deltaY = this.initialY - e.clientY
		  }
	}

	handleImageEndSliding() {
		let currentOpenIndex = null
		this.lookProductsList.forEach((elem, index) => {
			if (elem.classList.contains('is-active')) {
				currentOpenIndex = index
			}
		})
		if (this.deltaY > 0 && currentOpenIndex !== this.lookProductsList.length -1 && !this.didMove) {
			this.scrollToElement(currentOpenIndex + 1)
		} else if (this.deltaY < 0 && currentOpenIndex !== 0 && !this.didMove) {
			this.scrollToElement(currentOpenIndex - 1)
		}
		this.imageZoomContent.removeEventListener('mouseup', this.handleImageEndSliding)
	    this.imageZoomContent.removeEventListener('mouseleave', this.handleImageEndSliding)
	    this.imageZoomContent.removeEventListener('mousemove', this.handleImageSliding)
	    this.imageZoomContent.removeEventListener('touchmove', this.handleImageSliding)
	    this.imageZoomContent.removeEventListener('touchend', this.handleImageEndSliding)
	}

	scrollToElement(index) {
		const MIN_GAP = 5
		const currentOpenProduct = select('.look-detail-product__item.is-active')
		const productContent = select('.look-detail-product__content', this.lookProductsList[index])
		const minY = productContent.dataset.min ? productContent.dataset.min : ''
		let maxScrollTop = this.lookMainImg.offsetHeight - (window.innerHeight - productContent.offsetHeight)
		// checking the last product
        if ((parseInt(index)+1) === this.pageControlDots.length) {
          maxScrollTop = maxScrollTop + this.lastProductDelta
        }
		const scrollPosition = parseFloat(minY) * maxScrollTop / 100 + MIN_GAP
		this.didMove = true
		TweenLite.to(this.imageZoomContent, 1, {
		  scrollTop: scrollPosition,
		  ease: Quad.easeInOut
	    })
		this.updateMobileProductCard(this.lookProductsList[index], currentOpenProduct, index)
	}

	scrollToProduct(event) {
		const MIN_GAP = 5
		const MAX_GAP = MIN_GAP * 2
		const productIdClicked = event.target ? event.target.dataset.sectionid : ''
		const productIndex = event.target ? event.target.parentElement.dataset.number : ''
		const productListEl = select('#'+productIdClicked)
		const currentOpenProduct = select('.look-detail-product__item.is-active')
		if (productListEl !== null && productListEl !== currentOpenProduct) {
			const productContent = select('.look-detail-product__content', productListEl)
			const productTitle = select('.look-detail-product__title', productListEl)
			const minY = productContent.dataset.min ? productContent.dataset.min : ''
			if (minY !== '') {
			  let maxScrollTop = this.lookMainImg.offsetHeight - (window.innerHeight - productContent.offsetHeight)
              // checking the last product
              if ((parseInt(productIndex)+1) === this.pageControlDots.length) {
                maxScrollTop = maxScrollTop + this.lastProductDelta
              }
			  const scrollPosition = parseFloat(minY) * maxScrollTop / 100 + MIN_GAP
			  this.controlDotsClicked = true
			  TweenLite.to(this.imageZoomContent, 1, {
			      scrollTop: scrollPosition,
			      ease: Quad.easeInOut,
			      onComplete: () => {
			    	setTimeout(() => {
			    		this.controlDotsClicked = false
			    		focusElement(productTitle)
				}, 500)
			      }
			    })
			    this.updateMobileProductCard(this.lookProductsList[productIndex], currentOpenProduct, productIndex)
			}
		}
	}

	updateMobileProductCard(productToExpand, currentOpenProduct, lookIndexToExpand) {
		const BORDER_GAP = 10
		const paddingTop = 27
		let productWrapperHeight = this.productDetailsContainer.offsetHeight
		let totalProductTranform = 0
		currentOpenProduct.classList.remove('is-active')
		productToExpand.classList.add('is-active')
		this.activeTitle = select('.is-active .look-detail-product__title', this.popinEl)
	    this.activeDesc = select('.is-active .look-detail-product__description', this.popinEl)
	    this.activeref = select('.is-active .look-detail-product__ref', this.popinEl)
		const mobileIconWrapper = select('.mobile-icon-wrapper', this.productSectionWrapper)
		mobileIconWrapper.style.height = this.activeTitle.offsetHeight + this.activeref.offsetHeight + (this.activeDesc !== null ? this.activeDesc.offsetHeight : 0) + paddingTop + 'px'
		this.pageControlList.style.height = (window.innerHeight - productToExpand.offsetHeight + BORDER_GAP) + 'px'
		this.imageZoomContent.style.height = (window.innerHeight - productToExpand.offsetHeight + BORDER_GAP) + 'px'
		TweenLite.set(this.productDetailsContainer, { yPercent: `${(productWrapperHeight - productToExpand.offsetHeight) / productWrapperHeight* 100}%`})
		this.lookProductsList.every((elem) => {
			if (elem.classList.contains('is-active')) {
			  return false
			} else {
			  totalProductTranform = totalProductTranform + elem.offsetHeight
			  return true
			}
		})
		this.currentOpenProduct = productToExpand
		this.currentTransformValue = totalProductTranform
		TweenLite.set(this.productListContent, { y: `-${totalProductTranform}px`})
		this.updateScrollDots(lookIndexToExpand)
	}

	updateScrollDots = (activeIndex) => {
	  if (this.pageControlDots) {
		this.pageControlDots.forEach((bulletEl, index) => {
		  if (bulletEl.classList.contains('is-active') && index !== activeIndex) {
			bulletEl.classList.remove('is-active')
			bulletEl.querySelector('button').removeAttribute('aria-current')
		  } else if (activeIndex == index) {
		    bulletEl.classList.add('is-active')
		    bulletEl.querySelector('button').setAttribute('aria-current', true)
		  }
		})
	  }
	}

	closeLookPopin() {
	  document.body.classList.remove('has-modal')
	  this.popinEl.parentNode.removeChild(this.popinEl)
	  window.document.DLKit.push('on-tracking', {
		event: 'uaevent',
		eventAction: 'close',
		eventLabel: this.lookDetails.lookCode ? this.lookDetails.lookCode : '',
		eventCategory: 'pdp_look_popin'
	  })
	}

	expandClickedProduct(evt) {
	if (evt.type == 'click' || (evt.type == 'keyup' && (evt.which === 13 || evt.which === 32 ))) {
	  if (evt.currentTarget.getAttribute('aria-expanded') == "false") {
		let productToExpand = this.lookProductsList[evt.currentTarget.id.split('-')[2]]
		this.openedProductName = evt.currentTarget.innerText ? evt.currentTarget.innerText : ''
		this.productSectionClicked = true
		this.collapseAllPanels()
		this.expandCurrentPanel(productToExpand, evt.currentTarget)
		this.animateImage(productToExpand)
		this.sectionOpenDataLayer()
	  }
	}
  }

  collapseAllPanels() {
	this.lookProductsList.forEach((elem) => {
	  let lookExpandBtn = select('.look-detail-product__expand', elem)
	  if (lookExpandBtn.getAttribute('aria-expanded') === "true") {
		let lookDetailsCollapse = select('.look-detail-product__details',elem)
		lookExpandBtn.setAttribute('aria-expanded', false)
		lookDetailsCollapse.classList.remove('look-detail-product__details--enabled')
		select('.look-detail-product__title',elem).classList.remove('look-detail-product__title--active')
		TweenLite.to(lookDetailsCollapse, 0, {
		  display: 'none',
		  overflowY: 'hidden',
		  height:0,
		  paddingTop:0
		})
		lookExpandBtn.removeAttribute('aria-disabled')
		select('.icon-wrapper',elem).style.display = "block"
	  }
	})
  }

  expandCurrentPanel(productToExpand, clickedSpan) {
	let lookDetailsToExpand = select('.look-detail-product__details',productToExpand)
	clickedSpan.setAttribute('aria-expanded', true)
	lookDetailsToExpand.classList.add('look-detail-product__details--enabled')
	select('.look-detail-product__title',productToExpand).classList.add('look-detail-product__title--active')
	TweenLite.to(lookDetailsToExpand, 1, {
	  display: 'block',
	  overflowY: 'visible',
	  height:'auto',
	  onComplete: () => {
		let scrollProductSection = 0
		  if (productToExpand.getBoundingClientRect().top < 0) {
		    scrollProductSection = this.productWrapperDiv.scrollTop - Math.abs(productToExpand.getBoundingClientRect().top) - parseInt(window.getComputedStyle(productToExpand).marginBottom)
		    this.productWrapperDiv.scrollTo(0, scrollProductSection)
		  } else if (productToExpand.getBoundingClientRect().bottom > this.productWrapperDiv.offsetHeight) {
			  scrollProductSection = productToExpand.getBoundingClientRect().bottom
			  this.productWrapperDiv.scrollTo(0, scrollProductSection)
		  }
	  }
	})
	clickedSpan.setAttribute('aria-disabled', true)
	select('.icon-wrapper',productToExpand).style.display = "none"
  }

  animateImage(productWrapper) {
	const lookItem = select('.look-detail-product__content',productWrapper)
	const lookSpan = select('.look-detail-product__expand', productWrapper)
	const lookItemMin = lookItem.dataset.min ? parseFloat(lookItem.dataset.min) : 0
	const lookItemMax = lookItem.dataset.max ? parseFloat(lookItem.dataset.max) : 0
	const lookItemScrollCoef = lookItemMin + (lookItemMax - lookItemMin) / 2
	const maxScrollTop = this.lookMainImg.offsetHeight - window.innerHeight
	let scrollPosition
	if (this.lookProductsList.length == parseInt(lookSpan.id.split('-')[2]) + 1) {
		scrollPosition = Math.abs(lookItemMax * (maxScrollTop / 100))
	} else {
		scrollPosition = Math.abs(lookItemScrollCoef * (maxScrollTop / 100))
	}
	TweenLite.to(this.popinCol, 0.5, {
      scrollTop: scrollPosition,
      ease: Quad.easeInOut
    })
  }

  onImageScroll(event) {
    const currentScrollPosition = this.popinCol.scrollTop
	const maxScrollTop = this.lookMainImg.offsetHeight - window.innerHeight
	const scrollCoef = currentScrollPosition * 100 / maxScrollTop
	let lookIndexToExpand = 0
	this.lookItemsMinMax.forEach((element,index) => {
	  if (scrollCoef > element.min) {
	    if (index === this.lookItemsMinMax.length - 1) {
		  lookIndexToExpand = index
	    } else if(scrollCoef < this.lookItemsMinMax[index+1].min) {
		  lookIndexToExpand = index
	    }
	  }
	})
	const productToExpand = this.lookProductsList[lookIndexToExpand]
	const expandSpanElem = select('.look-detail-product__expand', productToExpand)
	if (expandSpanElem.getAttribute('aria-expanded') == "false") {
	  this.collapseAllPanels()
	  this.expandCurrentPanel(productToExpand, expandSpanElem)
	}
  }

  sectionOpenDataLayer() {
	window.document.DLKit.push('on-tracking', {
	  event: 'uaevent',
	  eventAction: 'display',
      eventLabel: this.openedProductName,
	  eventCategory: 'pdp_look_popin'
	})
  }

  handleSlidingStart = (e) => {
	  if (!e.touches) {
	    e.preventDefault()
	  }
	e.stopPropagation()
    this.initialX = e.touches ? e.touches[0].clientX : e.clientX
    this.initialY = e.touches ? e.touches[0].clientY : e.clientY
    this.deltaX = 0
    this.frameHeight = this.productDetailsContainer.offsetHeight
    if (e.currentTarget.classList.contains('mobile-icon-wrapper')) {
    	this.slidingElement = select('.mobile-icon-wrapper', this.productSectionWrapper)
    } else {
    	this.slidingElement = this.productDetailsContainer
    }
    this.previousYTransform = this.transformY
    this.initialScrollTop = this.productDetailsContainer.scrollTop
    this.slidingElement.addEventListener('mouseup', this.handleEndSliding)
    this.slidingElement.addEventListener('mouseleave', this.handleEndSliding)
    this.slidingElement.addEventListener('mousemove', this.handleSliding)
    this.slidingElement.addEventListener('touchmove', this.handleSliding)
    this.slidingElement.addEventListener('touchend', this.handleEndSliding)
  }

  handleSliding = (e) => {
	if (e.touches) {
	  this.deltaX = this.initialX - e.touches[0].clientX
	  this.deltaY = this.initialY - e.touches[0].clientY
	} else {
	  this.deltaX = this.initialX - e.clientX
	}
	if (this.deltaY > 0 && e.currentTarget.classList.contains('mobile-icon-wrapper')) {
	  this.transformY = (((this.deltaY)) / this.frameHeight * 100)
	  this.transformY = this.transformPercent - this.transformY
	} else if (this.deltaY < 0 && e.currentTarget.classList.contains('look-popin-products__container') && this.productDetailsContainer.scrollTop <= 0) {
		this.deltaY = this.deltaY + this.initialScrollTop
		this.transformY = (((this.deltaY)) / this.frameHeight * 100)
		this.transformY = Math.abs(this.transformY)
		this.toggleBackGroundContent('show')
		this.toggleLookHeader('hide')
	}
	if (this.transformY < this.transformPercent && this.transformY > 0) {
	  TweenLite.set(this.productListContent, { y: 0})
	  TweenLite.set(this.productDetailsContainer, {yPercent: this.transformY})
	}
	this.markAllProductActive()
  }

  handleEndSliding = (e) => {
	const slideDirectionCoef = this.transformY - this.previousYTransform
	if (slideDirectionCoef < 0 && Math.abs(slideDirectionCoef) > 15 ) {
	  this.showProductDetails()
	} else if (slideDirectionCoef > 0 && Math.abs(slideDirectionCoef) > 15 ) {
	  this.collapseListPanel()
	} else if (Math.abs(slideDirectionCoef) > 0 && Math.abs(slideDirectionCoef) <= 15) {
	  TweenLite.set(this.productDetailsContainer, {yPercent: 0})
	  this.toggleBackGroundContent('hide')
	  this.toggleLookHeader('show')
	}
  }

  showProductDetails = () => {
	const topPanel = select('.collapse-product-panel')
	const mobileIconWrapper = select('.mobile-icon-wrapper', this.productSectionWrapper)
	TweenLite.set(this.productListContent, { y: 0})
	this.toggleLookHeader('show')
	TweenLite.to(this.productDetailsContainer , 0.5, {
	  yPercent: 0,
	  overflowY : 'auto',
	  onComplete: () => {
		this.transformY = 0
		this.buttonClose.style.visibility = "hidden"
		this.navPageControl.style.visibility = "hidden"
		this.lookMainImg.style.visibility = "hidden"
		this.slidingElement.removeEventListener('touchstart', this.handleSlidingStart)
        this.slidingElement.removeEventListener('mousedown', this.handleSlidingStart)
		this.slidingElement.removeEventListener('mouseup', this.handleEndSliding)
        this.slidingElement.removeEventListener('mouseleave', this.handleEndSliding)
        this.slidingElement.removeEventListener('mousemove', this.handleSliding)
        this.slidingElement.removeEventListener('touchmove', this.handleSliding)
        this.slidingElement.removeEventListener('touchend', this.handleEndSliding)
        this.productDetailsContainer.addEventListener('touchstart', this.handleSlidingStart)
        this.productDetailsContainer.addEventListener('mousedown', this.handleSlidingStart)
        mobileIconWrapper.firstElementChild.setAttribute('aria-expanded', true)
		mobileIconWrapper.style.display = 'none'
		mobileIconWrapper.firstElementChild.style.display = 'none'
    this.productDetailsContainer.classList.add('is-expanded')
        focusElement(topPanel, {notChangeTabIndex: true})
	  }
     })
	this.markAllProductActive()
  }

  collapseListPanel = () => {
	const mobileIconWrapper = select('.mobile-icon-wrapper', this.productSectionWrapper)
	let productWrapperHeight = this.productDetailsContainer.offsetHeight
	let currentOffset = this.currentOpenProduct ? this.currentOpenProduct.offsetHeight : this.lookProductsList[0].offsetHeight
	this.productDetailsContainer.scrollTop = 0
	this.toggleLookHeader('hide')
	TweenLite.set(this.productListContent, { y: `-${this.currentTransformValue}px`})
	TweenLite.to(this.productDetailsContainer, 0.5, {
	  yPercent: `${(productWrapperHeight - currentOffset) / productWrapperHeight* 100}%`,
	  overflowY : 'hidden',
	  onComplete: () => {
		this.hideProductList()
		this.buttonClose.style.visibility = "visible"
		this.navPageControl.style.visibility = "visible"
		this.lookMainImg.style.visibility = "visible"
		this.transformY = (productWrapperHeight - currentOffset) / productWrapperHeight* 100
		mobileIconWrapper.firstElementChild.style.display = 'block'
		mobileIconWrapper.addEventListener('touchstart', this.handleSlidingStart)
        mobileIconWrapper.addEventListener('mousedown', this.handleSlidingStart)
        mobileIconWrapper.firstElementChild.setAttribute('aria-expanded', false)
		mobileIconWrapper.style.display = 'block'
    this.productDetailsContainer.classList.remove('is-expanded')
        focusElement(mobileIconWrapper.firstElementChild, {notChangeTabIndex: true})
	  }
    })
  }

  toggleBackGroundContent(status) {
	  if (status == 'hide') {
		  this.buttonClose.style.visibility = "hidden"
		  this.navPageControl.style.visibility = "hidden"
		  this.lookMainImg.style.visibility = "hidden"
	  } else if (status == 'show') {
		  this.buttonClose.style.visibility = "visible"
		  this.navPageControl.style.visibility = "visible"
		  this.lookMainImg.style.visibility = "visible"
	  }
  }

  toggleLookHeader(status) {
	  const topPanel = select('.collapse-product-panel')
	  if (status == 'show') {
		TweenLite.to(this.listCollapseHeader, 0.5, {
			y: 0,
			display: 'block',
			onComplete: () => {
				topPanel.setAttribute('aria-expanded', true)
			    topPanel.firstElementChild.classList.remove('is-sr-only')
			}
		})
	  } else if (status == 'hide') {
		TweenLite.to(this.listCollapseHeader, 0.5, {
			y: `-${this.listCollapseHeader.offsetHeight}px`,
			display: 'none',
			onComplete: () => {
				topPanel.setAttribute('aria-expanded', true)
			    topPanel.firstElementChild.classList.remove('is-sr-only')
			}
		})
	  }
  }

  markAllProductActive() {
	if (this.lookProductsList.length > 0) {
	  this.lookProductsList.forEach((elem) => {
		elem.classList.add('is-active')
	  })
	}
  }

  hideProductList() {
	if (this.lookProductsList.length > 0) {
	  this.lookProductsList.forEach((elem) => {
		if(this.currentOpenProduct !== null && this.currentOpenProduct.id !== elem.id) {
		  elem.classList.remove('is-active')
		}
	  })
	}
  }
}


const init = () => {
	const lookDetailsModal = new ViewDetailsModal()
	lookDetailsModal.init().then(() => {
		lookDetailsModal.open()
	})
}

export default init
