import { HEADER_SIZES, LOOK_STICKY_SIZES } from 'config/constants'
import {
  isLTR,
  isMobile,
  isiOS,
  isWideScreen,
  isTabletScreen,
  isMobileScreen,
  focusElement,
  scrollDirectionDetector,
  isInViewPort
} from 'lib/helpers'
import {
  Manager,
  WINDOW_WAS_RESIZED,
  BREAKPOINT_CHANGE,
  PAGELOAD_WISHLIST,
  WINDOW_DID_SCROLL
} from 'lib/events'
import CacheIndex from 'modules/cache/pages/index'
import { select, selectAll, getCollection } from 'lib/selectors'
import ScrollSpy from 'lib/scroll-spy'
import SyncCols from 'ui/widgets/sync-cols'
import bind from 'delegate'
import Zoom from '../pdp/zoom'
import lookModalInit from './look-detail-modal'
import * as xhr from 'modules/fetch'
import config from 'lib/config'
import pageTitle from 'modules/pageTitle'
import { cloneDeep, debounce } from 'lodash'
import { init } from 'ui/video'

export default class LookDetailController {
  constructor(container) {
    this.container = container
    this.lookPageName = 'look-page'
    this.enableNewPdp = this.container.classList.contains(`${this.lookPageName}__new-pdp`)
    this.lookPageWithProducts = this.container.classList.contains(
      `${this.lookPageName}__with-products`
    )
    this.lookPageWithImages = this.enableNewPdp
      ? this.container.classList.contains(`${this.lookPageName}__with-show`)
      : this.container.classList.contains(`${this.lookPageName}__with-images`)
    this.lookPageWrapper = select('.look-page')
    this.pageControlsWrapper = select(`.${this.lookPageName}__controls__wrapper`, this.container)
    this.pageTitleWrapper = select(`.${this.lookPageName}__details-wrapper`, this.container)
    this.lookProductMainImgContainer = select(`.${this.lookPageName}__main-img`, this.container)
    this.lookMainZoomableImage = select(`.${this.lookPageName}__img-zoom img`, this.container)
    this.pageControlBtn = selectAll(`.${this.lookPageName}__control-btn`, this.container)
    this.lookVideoContainer = select('.look-page__video-wrapper', this.container)
    this.lookItems = selectAll('.look-description', this.pageTitleWrapper)
    this.lookItemsLi = selectAll(
      `.${this.lookPageName}__details-wrapper-list`,
      this.pageTitleWrapper
    )
    this.lookItemsMinMax = this.lookItems
      ? Array.from(this.lookItems).map(element => ({ min: element.dataset.min, max: element.dataset.max }))
      : []
    if (this.lookPageWithProducts && !this.enableNewPdp) {
      this.lastLookProduct = this.lookItems[this.lookItems.length - 1].closest('li')
      this.mobileContentMainImgHeight = select(`.${this.lookPageName}__main-img`).offsetHeight
      this.lastMobileProductMinMax = select('.look-description', this.lastLookProduct).dataset
    }
    this.btnZoom = select('.look-btn-zoom', this.container)
    this.videoContainerBtn = select('.video-container', this.container)
    // swipe look changes goes here
    this.lookSlideShowWrapper = select('.look-slideshow__wrapper', this.container)
    this.swipeLookSlides = getCollection('.look-slideshow__common', this.container)
    this.currentLookSlide = select('.look-slideshow__current', this.container)
    this.nextLookSlide = select('.look-slideshow__next', this.container)
    this.prevLookSlide = select('.look-slideshow__prev', this.container)
    this.navigationWrapper = select('.look-page__look-navigation')
    this.indexControlImage = 0
    this.zoomArray = []
    this.syncColsDots = null
    this.syncColsTitle = null
    this.scrollSpyDots = null
    this.scrollSpyMobile = null
    this.currentAnimationState = null
    this.currentCoefScale = 0
    this.isTranslate = true
    this.visibleDiff = null
    this.scrollDiff = null
    this.timeoutScrollMobile = null
    this.currentMobileActiveProduct = null
    this.oldMobileActiveProduct = null
    this.isLastElementMobileStatic = false
    this.controlDots = selectAll('li', this.pageControlsWrapper)
    // lookSwipeVariable
    this.currentCard = null
    this.previousCard = null
    this.nextCard = null
    this.lookDetailsWrapper = null
    this.nextLookLink = null
    this.previousLookLink = null
    this.initialWidth = null
    this.imageBaseUrl = null
    this.nextLookImages = null
    this.previousLookImages = null
    this.currentLookImages = null
    this.nextLookCode = null
    this.previousLookCode = null
    this.currentLookCode = null
    this.newLookdata = null
    this.lookCardTemplate = null
    this.transformation = null
    this.currentLookId = null
    this.nextLookId = null
    this.previousLookId = null
    this.initialX = 0
    this.initialY = 0
    this.deltaX = 0
    this.frameWidth = 0
    this.transformX = 0
    this.nextElementToLoad = null
    this.allowSwipeRight = true
    this.allowSwipeLeft = true
    this.wishListData = null
    this.nextLookVideo = null
    this.previousLookVideo = null
    this.currentLookVideo = null
    this.nextLookVideoConfig = null
    this.previousLookVideoConfig = null
    this.currentLookVideoConfig = null
    this.mobileImageWrapper = null
    this.mobileCardWrapper = null
    this.windowHeight = window.innerHeight;
    this.mobileCardContainer = select('.look-page__details-container')
    this.defaultImgHeight = select('.look-slideshow__current .PACKSHOT_DEFAULT').getBoundingClientRect().height
    this.heightDifference = this.windowHeight - this.defaultImgHeight
    this.alreadyBindScroll = false
  }

  enter = () => {
    if (!this.container) {
      return
    }
    // init page controls dots
    let scrollSpyElement
    if (!this.enableNewPdp) {
      this.transformLookItemsForMobile()
      if (this.lookPageWithImages) {
        scrollSpyElement = select('.look-page__alternative-imgs-container')
      } else if (this.lookPageWithProducts) {
        scrollSpyElement = select('.look-page__details-wrapper')
      }
    } else {
      this.toggleMobileLookCards()
      scrollSpyElement = select('.look-page__images-container')
    }
    const mediaImages = [
      this.lookProductMainImgContainer,
      ...Array.from(selectAll('.look-page__img-wrapper', scrollSpyElement))
    ]
    const self = this
    const videoEl = select('video', mediaImages.filter(e => select('video', e))[0])
    if (videoEl) {
      const videoElRef = select('.js-video', this.lookVideoContainer)
      const videoConfigData = videoElRef ? videoElRef.dataset.config : ''
      const videoTitleData = videoConfigData ? JSON.parse(videoConfigData).title : ''
      const videoIconLookNumber = select('.video-container')
        ? select('.video-container').dataset.lookNumber
        : ''
      let videoAriaLabel = config.popinLookPDP.videoAriaLabel
        .replace('{0}', 1)
        .replace('{1}', parseInt(videoIconLookNumber))
      if (videoTitleData) {
        videoAriaLabel = videoAriaLabel.concat(` - ${videoTitleData}`)
      }
      videoEl.setAttribute('aria-label', videoAriaLabel)
    }
    if (mediaImages && mediaImages.some(e => window.getComputedStyle(e).height === '0px')) {
      if (videoEl) {
        videoEl.addEventListener(
          'loadeddata',
          () => {
            self.initSyncControlsDots()
            self.initSyncColsTitle()
          },
          false
        )
      }
    } else {
      this.initSyncControlsDots()
      this.initSyncColsTitle()
    }
    // init page title scroll

    // init scrollAnimation on mobile
    // this.initScrollSpyMobile()

    this.bind()
    this.handlePlayPauseButtons()
  }

  calcPdpCardPosition = () => {
    const lookPdpImgs = getCollection('li',select('.look-page__images-container.look-slideshow__current'))
    const mobileHeightWrapper = select('.look-page__mobile-height').getBoundingClientRect().height
    const isHeightSmall = mobileHeightWrapper < this.windowHeight
    if(lookPdpImgs.length === 1 && isHeightSmall) {
      this.mobileCardContainer.style.position = 'absolute'
    } else {
      this.mobileCardContainer.style.position = 'fixed'
    }
  }

  isNotWideScreen = () => {
    if (!isWideScreen() && this.enableNewPdp) {
      let timer = null
      let that = this
      this.previousLookLink = select('.js-previous-look')
      this.nextLookLink = select('.js-next-look')
      this.lookDetailsWrapper = select('.look-page__details-wrapper-inner')
      this.mobileImageWrapper = select('.look-page__mobile-height')
      this.mobileCardWrapper = select('.look-page__details-container')
      const bindScroll = () => {
        window.onscroll = function () {
          if (isWideScreen()) return
          const contentHeight = that.mobileImageWrapper.offsetHeight - that.heightDifference;
          const isVisible = window.scrollY <= contentHeight
          that.mobileCardWrapper.classList.toggle('is-d-block', isVisible)
          that.mobileCardWrapper.classList.toggle('is-d-none', !isVisible)
          that.lookDetailsWrapper.classList.add('has-opacity-hidden')
        }

        window.addEventListener('scroll', function(e) {
          if (timer) {
            clearTimeout(timer)
          }
          timer = setTimeout(function() {
            that.lookDetailsWrapper.classList.remove('has-opacity-hidden')
          }, 150)
          e.preventDefault
        }, false)
      }


      if(!this.alreadyBindScroll) {
        bindScroll()
        this.alreadyBindScroll = true
      }
      this.correctLookData()
      this.calcPdpCardPosition()
    }
  }


  bind = () => {
    Manager.subscribe(BREAKPOINT_CHANGE, this.onBreakpointChange)
    if (this.lookVideoContainer) {
      this.lookVideoContainer.addEventListener('keyup', this.onButtonVideoPlayPause)
      this.lookVideoContainer.addEventListener('click', this.onButtonVideoPlayPause)
      this.videoContainerBtn.addEventListener('keyup', this.onButtonVideoPlayPause)
      this.videoContainerBtn.addEventListener('click', this.onButtonVideoPlayPause)
    }
    if (this.lookPageWithProducts && !this.enableNewPdp) {
      const isDataMaxSet = !Array.from(this.lookItems)
        .slice(1)
        .some(e => !e.dataset.max)
      const lastLookItemId = `look-details-${this.lookItems.length}`
      const lastLookItemEl = select(
        `li#look-details-${this.lookItems.length}`,
        this.pageTitleWrapper
      )
      bind(this.pageTitleWrapper, 'a[href*="#look-details-"]', 'click', e =>
        this.goToActiveLookElement(e, isDataMaxSet, lastLookItemId, lastLookItemEl)
      )
    }
    if (this.lookPageWithProducts || this.lookPageWithImages) {
      bind(this.pageControlsWrapper, 'a[href*="#look-details-"]', 'keyup', this.handleControlsKeyUp)
    }
    if (
      this.lookPageWithProducts &&
      (isMobileScreen() || isTabletScreen()) &&
      this.lookMainZoomableImage
    ) {
      if (isiOS()) {
        this.lookMainZoomableImage.classList.add('ios')
      }
      this.animateImageOnMobile()
      window.addEventListener('scroll', this.animateImageOnMobile)
    }
    const lookCards = getCollection('.look-page__card-wrapper')
    if (!isWideScreen() && this.enableNewPdp) {
      let timer = null
      const that = this
      const lookPageWrapper = select('.look-page')
      const lookPageSticky = select('.js-sticky-item')
      if (lookPageSticky !== null && lookPageSticky !== undefined) {
        lookPageSticky.parentNode.removeChild(lookPageSticky)
      }
      this.previousLookLink = select('.js-previous-look')
      this.nextLookLink = select('.js-next-look')
      this.lookDetailsWrapper = select('.look-page__details-wrapper-inner')
      this.mobileImageWrapper = select('.look-page__mobile-height')
      this.mobileCardWrapper = select('.look-page__details-container')
      this.lookPageWrapper.addEventListener('touchstart', this.handleSlidingStart)
      this.lookPageWrapper.addEventListener('mousedown', this.handleSlidingStart)
      this.previousLookLink
        ? this.previousLookLink.addEventListener('click', this.loadPrevLook.bind(this))
        : ''
      this.previousLookLink
        ? this.previousLookLink.addEventListener('keypress', e => {
          if (e.keyCode == 32) {
            e.preventDefault()
            that.loadPrevLook(e)
          }
        })
        : ''
      this.nextLookLink
        ? this.nextLookLink.addEventListener('click', this.loadNextLook.bind(this))
        : ''
      this.nextLookLink
        ? this.nextLookLink.addEventListener('keypress', e => {
          if (e.keyCode == 32) {
            e.preventDefault()
            that.loadNextLook(e)
          }
        })
        : ''

      window.onscroll = function () {
        if (isWideScreen()) return
        const contentHeight = that.mobileImageWrapper.offsetHeight - that.heightDifference;
        const isVisible = window.scrollY <= contentHeight
        that.mobileCardWrapper.classList.toggle('is-d-block', isVisible)
        that.mobileCardWrapper.classList.toggle('is-d-none', !isVisible)
        that.lookDetailsWrapper.classList.add('has-opacity-hidden')
      }

      window.addEventListener(
        'scroll',
        () => {
          if (timer) {
            clearTimeout(timer)
          }
          timer = setTimeout(() => {
            that.lookDetailsWrapper.classList.remove('has-opacity-hidden')
          }, 150)
        },
        false
      )
      Manager.subscribe(PAGELOAD_WISHLIST, wishlistData => {
        this.wishListData = wishlistData.entries.map(product => product.code)
      })
      Manager.subscribe(WINDOW_DID_SCROLL, this.togglePlayPauseIcon.bind(this))
      // remove next and previous arrow icons for mobile view
      lookCards.forEach(elem => {
        const wishListBtn = select('button.is-wishlist', elem)
        if (wishListBtn && !elem.classList.contains('current-card')) {
          wishListBtn.setAttribute('tabindex', -1)
        }
      })
      this.slideLookData()
      this.calcPdpCardPosition()
    } else {
      lookCards.forEach(elem => {
        if (elem.classList.contains('is-hidden-desktop')) {
          elem.parentNode.removeChild(elem)
        } else {
          this.removeDataAttribute(elem)
        }
      })
    }
    window.addEventListener('resize', debounce(() => {

      const mobileLookCardWrapper = select('.look-page__details-wrapper-inner')
      if (window.innerHeight - mobileLookCardWrapper.getBoundingClientRect().bottom < 50) {
          mobileLookCardWrapper.style.position = 'unset'
      }
      if(isWideScreen()) {
          mobileLookCardWrapper.style.position = 'unset'
    }
      this.isNotWideScreen()

      if (!isWideScreen()) return
      this.mobileCardWrapper = select('.look-page__details-container')
      if (this.mobileCardWrapper) {
        this.mobileCardWrapper.style.position = 'unset'
      }
    }, 50))

    window.addEventListener('scroll', function() {
      const mobileLookCardWrapper = select('.look-page__details-wrapper-inner')
      if(!isWideScreen()) {
          mobileLookCardWrapper.style.position = 'absolute'
      } else {
        mobileLookCardWrapper.style.position = 'unset'
      }
    }, false)


    if (this.enableNewPdp) {
      this.setZoomImages()
      bind(document.body, '.js-view-detail-modal', 'click', lookModalInit)
      let lastElementReached = false
      this.pageControlBtn.forEach(elem => {
        elem.addEventListener('keyup', this.focusMediaOnClick, true)
        elem.addEventListener('click', this.focusMediaOnClick, true)
      })
      bind(this.lookProductMainImgContainer, '.look-page__img-wrapper', 'keyup', e => {
        if (e.which === 32) {
          const currentActiveDot = this.controlDots.filter(elem =>
            elem.classList.contains('is-active')
          )
          if (currentActiveDot) {
            if (
              parseInt(currentActiveDot[0].dataset.number) + 1 === this.controlDots.length &&
              !lastElementReached
            ) {
              window.scrollTo({
                left: 0,
                top: select(`#look-details-${parseInt(currentActiveDot[0].dataset.number)}`)
                  .offsetTop,
                behavior: 'smooth'
              })
              focusElement(
                select(`#look-details-${parseInt(currentActiveDot[0].dataset.number)} img`)
              )
            } else if (
              parseInt(currentActiveDot[0].dataset.number) + 1 !==
              this.controlDots.length
            ) {
              window.scrollTo({
                left: 0,
                top: select(`#look-details-${parseInt(currentActiveDot[0].dataset.number)}`)
                  .offsetTop,
                behavior: 'smooth'
              })
              focusElement(
                select(`#look-details-${parseInt(currentActiveDot[0].dataset.number)} img`)
              )
            }
            if (parseInt(currentActiveDot[0].dataset.number) + 1 === this.controlDots.length) {
              lastElementReached = true
            } else {
              lastElementReached = false
            }
          }
        }
      })
    }
  }

  slideLookData() {
    this.currentCard = select('.look-page__card-wrapper.current-card')
    this.previousCard = select('.look-page__card-wrapper.prev-card')
    this.nextCard = select('.look-page__card-wrapper.next-card')
    this.lookSlideShowWrapper.style.transform = 'translateX(0%)'
    this.imageBaseUrl = this.currentCard.dataset.baseurl
    this.transformation = this.currentCard.dataset.looktransform
    this.nextLookCode = this.nextCard.dataset.code
    this.previousLookCode = this.previousCard.dataset.code
    this.currentLookCode = this.currentCard.dataset.code
    this.nextLookElement = this.currentCard.dataset.nextelement
    this.previousLookElement = this.currentCard.dataset.previouselement
    this.currentLookElement = this.previousCard.dataset.nextelement
    this.nextElementToLoad = this.nextCard.dataset.nextelement
    this.previousElementToLoad = this.previousCard.dataset.previouselement
    this.lookCardTemplate = select('.look-description__card-container', this.currentCard)
    this.currentLookId = this.currentCard.dataset.lookid
    this.nextLookId = this.nextCard.dataset.lookid
    this.previousLookId = this.previousCard.dataset.lookid
    if (this.previousLookElement == '') {
      this.removePreviousCard()
    } else if (this.nextLookElement == '') {
      this.removeNextCard()
    }
    this.removeDataAttribute(this.currentCard)
    this.removeDataAttribute(this.previousCard)
    this.removeDataAttribute(this.nextCard)
    this.correctLookData()
  }

  formatImages(imageArr) {
    if (imageArr && Object.keys(JSON.parse(imageArr)).length !== 0) {
      const cloudinaryQuality =
        `${window.config.cloudinary.defaultQuality
        }/w_${
        window.config.cloudinary.defaultSrcFallBack
        }/`
      const imagesArray = JSON.parse(imageArr)
      let defaultImage
      const newImages = []
      const newVideo = null
      const assetType = Object.keys(imagesArray)
      for (let index = 0; index < assetType.length; index++) {
        if (assetType[index] == 'PACKSHOT_DEFAULT' && this.transformation !== '') {
          defaultImage =
            `${this.imageBaseUrl
            }/` +
            `t_${
            this.transformation
            }/${
            cloudinaryQuality
            }${imagesArray[assetType[index]]}`
        } else if (assetType[index] !== 'VIDEO') {
          newImages.push(
            `${this.imageBaseUrl}/${cloudinaryQuality}${imagesArray[assetType[index]]}`
          )
        }
      }
      defaultImage !== undefined ? newImages.unshift(defaultImage) : newImages
      return newImages
    }
  }

  configureVideo(videoData, lookCode) {
    if (videoData) {
      let formattedVideoData
      if (typeof videoData === 'string') {
        formattedVideoData = JSON.parse(videoData)
      } else {
        formattedVideoData = {
          config: {
            autoSized: true,
            loop: true,
            mute: true,
            fluidLoop: true,
            videoCentered: true,
            pauseScroll: true,
            controls: {
              mode: 'none',
              volume: videoData[0].soundControl || false,
              subtitles: videoData[0].subTitleAndLang || false,
              fullscreen: videoData[0].fullControl || false
            },
            sound: videoData[0].autoSound || false,
            asBackground: 'true',
            playButtonStyle: videoData[0].playButtonText ? 'none' : '',
            playbutton: {
              color: videoData[0].playButtonColor && videoData[0].playButtonColor.toLowerCase() || "",
              position: videoData[0].playButtonPosition.toLowerCase()
            },
            coverImage: videoData[0].posterFrame ? videoData[0].posterFrame.seoURL : '',
            loader: {
              type: videoData[0].loaderIcon != 'wfj' ? 'default' : 'brand'
            },
            soundcredit: videoData[0].soundCredit || false
          },
          brightcoveId: videoData[0].brightcoveId,
          accountId: videoData[0].accountId
            ? videoData[0].accountId
            : window.config.popinLookPDP.videoAccountId,
          title: videoData[0].title ? videoData[0].title : '',
          description: videoData[0].description ? videoData[0].description : ''
        }
      }

      const brightcoveId = formattedVideoData.brightcoveId
      const accountId = formattedVideoData.accountId
      const videoConfig = formattedVideoData.config
      let videoAriaLabel = window.config.popinLookPDP.videoAriaLabel
        .replace('{0}', 1)
        .replace('{1}', parseInt(lookCode))
      let lookVideoDescription = ''
      if (formattedVideoData.title) {
        videoAriaLabel = `${videoAriaLabel} - ${formattedVideoData.title}`
      }
      if (formattedVideoData.description) {
        lookVideoDescription =
          `<div class="is-sr-only">${formattedVideoData.description}</div>`
      }
      const videoId = `video_look-details-video_${parseInt(lookCode)}`
      return (
        `<div id="${
        videoId
        }" class="video js-video" data-ooyalaId=${
        brightcoveId
        } data-ooyalaAccountId=${
        accountId
        } data-config=${
        JSON.stringify(videoConfig)
        } data-decorative="true" data-aria-label="${
        videoAriaLabel
        }"></div>`
      )
    }
  }

  removeDataAttribute(elem) {
    for (const key in elem.dataset) {
      elem.removeAttribute(
        `data-${
          key
            .split(/(?=[A-Z])/)
            .join('-')
            .toLowerCase()}`
      )
    }
  }

  handleSlidingStart = e => {
    if (!e.touches) {
      e.preventDefault()
    }
    this.initialX = e.touches ? e.touches[0].clientX : e.clientX
    this.initialY = e.touches ? e.touches[0].clientY : e.clientY
    this.deltaX = 0
    this.frameWidth = this.lookProductMainImgContainer.clientWidth

    this.lookPageWrapper.addEventListener('mouseup', this.handleEndSliding)
    this.lookPageWrapper.addEventListener('mouseleave', this.handleEndSliding)
    this.lookPageWrapper.addEventListener('mousemove', this.handleSliding)
    this.lookPageWrapper.addEventListener('touchmove', this.handleSliding, { passive: false })
    this.lookPageWrapper.addEventListener('touchend', this.handleEndSliding)
    this.setTopProperty()
  }

  handleSliding = e => {
    const scrollThresold = 10
    if (e.touches) {
      this.deltaX = this.initialX - e.touches[0].clientX
      this.deltaY = this.initialY - e.touches[0].clientY
    } else {
      this.deltaX = this.initialX - e.clientX
    }
    if (
      Math.abs(this.deltaY) < scrollThresold + 50 &&
      Math.abs(this.deltaX) > Math.abs(this.deltaY)
    ) {
      this.transformX = ((this.deltaX * -1) / this.frameWidth) * 100
      if (
        (this.allowSwipeRight && this.transformX > 0) ||
        (this.allowSwipeLeft && this.transformX < 0)
      ) {
        document.body.classList.add('stop-scrolling')
        TweenLite.set(this.lookProductMainImgContainer, { xPercent: this.transformX })
        TweenLite.set(this.lookDetailsWrapper, { xPercent: this.transformX })
      } else {
        this.transformX = 0
      }
      if (isiOS()) {
        e.preventDefault()
        e.stopPropagation()
      }
    }
  }

  handleEndSliding = e => {
    if (this.transformX <= -15) {
      this.allowSwipeLeft = false
      // this.loadNextLook(e)
      isLTR() ? this.loadNextLook(e) : this.loadPrevLook(e)

    } else if (this.transformX >= 15) {
      this.allowSwipeRight = false
      // this.loadPrevLook(e)
      isLTR() ? this.loadPrevLook(e) : this.loadNextLook(e)

    } else if (
      (this.transformX > 0 && this.transformX < 25) ||
      (this.transformX < 0 && this.transformX > -25)
    ) {
      TweenLite.to([this.lookProductMainImgContainer, this.lookDetailsWrapper], 0.5, {
        xPercent: 0,
        ease: Quad.easeInOut,
        onComplete: () => document.body.classList.remove('stop-scrolling'),
      })
    } else {
      document.body.classList.remove('stop-scrolling')
    }
    const btnPlay = select('button.video-container')
    btnPlay ? btnPlay.addEventListener('click', this.onButtonVideoPlayPause, true) : ''
    this.lookPageWrapper.removeEventListener('mouseup', this.handleEndSliding)
    this.lookPageWrapper.removeEventListener('mouseleave', this.handleEndSliding)
    this.lookPageWrapper.removeEventListener('mousemove', this.handleSliding)
    this.lookPageWrapper.removeEventListener('touchmove', this.handleSliding)
    this.lookPageWrapper.removeEventListener('touchend', this.handleEndSliding)
  }

  setTopProperty = () => {
    const currentScrollTop = this.currentLookSlide.getBoundingClientRect().y
    this.prevLookSlide
      ? (this.prevLookSlide.style.top =
          `${Math.abs(currentScrollTop - HEADER_SIZES.small_collapsed)}px`)
      : ''
    this.nextLookSlide
      ? (this.nextLookSlide.style.top =
          `${Math.abs(currentScrollTop - HEADER_SIZES.small_collapsed)}px`)
      : ''
  }

  loadNextLook(event) {
    event.target.tagName == 'A' ? event.preventDefault() : ''
    TweenLite.to([this.lookProductMainImgContainer, this.lookDetailsWrapper], 0.5, {
      xPercent: isLTR() ? -100 : 100,
      ease: Quad.easeInOut,
      onComplete: () => {
        let newLookData = null
        const newLookImages = {}
        let imageData = null
        let lookVideo = null
        document.body.classList.remove('stop-scrolling')
        pageTitle(config.popinLookPDP.lookCardNumber.replace('{0}', parseInt(this.nextLookCode)))
        if (this.nextElementToLoad !== null && this.nextElementToLoad !== '') {
          xhr
            .get(config.popinLookPDP.lookSwipeData.replace('{0}', this.nextElementToLoad))
            .then(data => {
              newLookData = data
              if (newLookData.lookImages !== null && newLookData.lookImages !== undefined) {
                newLookData.lookImages.map((elem, index) => {
                  elem.assetType = `${elem.assetType}_${index}`
                })
              }
              // set image data for show and boutique looks
              if (newLookData.lookImages === undefined && newLookData.allImages !== undefined) {
                imageData = newLookData.allImages
              } else if (
                newLookData.lookImages !== undefined &&
                newLookData.allImages !== undefined
              ) {
                imageData = newLookData.allImages.concat(newLookData.lookImages)
              } else if (newLookData.lookImages !== undefined) {
                imageData = newLookData.lookImages
              }

              if (newLookData.lookVideos !== undefined && newLookData.lookVideos.length !== 0) {
                lookVideo = this.configureVideo(newLookData.lookVideos, newLookData.passageNumber)
              } else if (newLookData.videos !== undefined && newLookData.videos.length !== 0) {
                lookVideo = this.configureVideo(newLookData.videos, newLookData.passageNumber)
              }

              if (imageData !== '' && imageData !== null) {
                const sortedImageData = imageData.sort((a, b) => a.galleryIndex - b.galleryIndex)
                sortedImageData.forEach(elem => {
                  if (elem.assetType !== 'VIDEO') newLookImages[elem.assetType] = elem.path
                })
              }
            })
            .then(() => {
              if (!this.allowSwipeRight) {
                this.allowSwipeRight = true
                this.previousCard.style.display = 'block'
                this.previousLookLink.style.display = 'block'
              }
              this.updateNextLookData(newLookData, newLookImages, lookVideo)
            })
            .then(() => {
              this.mobileLookTemplate(
                this.nextLookImages,
                this.nextLookCode,
                this.nextLookVideo,
                'next'
              )
              this.updateLookCard(this.nextLookCode, this.nextLookElement, this.nextCard)
              this.setupLookVideo('next')
              this.setZoomImages(this.currentLookCode)
              window.scrollTo(0, 0)
              setTimeout(() => {
                this.previousCard.style.removeProperty('transform')
                this.nextCard.style.removeProperty('transform')
                this.navigationWrapper.style.display = 'block'
                this.allowSwipeLeft = true
              }, 250)
            })
        } else {
          this.updateNextLookData(newLookData, newLookImages, lookVideo)
          this.removeNextSlideData()
          this.removeNextCard()
          this.setZoomImages(this.currentLookCode)
          this.setupLookVideo('next')
          window.scrollTo(0, 0)
          this.previousCard.style.removeProperty('transform')
          this.nextCard.style.removeProperty('transform')
          this.navigationWrapper.style.display = 'block'
        }
      }
    })
  }

  removeNextCard() {
    this.nextCard.style.display = 'none'
    this.nextLookLink.style.display = 'none'
    this.allowSwipeLeft = false
  }

  removeNextSlideData() {
    while (this.nextLookSlide.firstChild) {
      this.nextLookSlide.removeChild(this.nextLookSlide.lastChild)
    }
  }

  updateNextLookData = async (lookData, lookImages, lookVideo) => {
    await new Promise(resolve => {
      TweenLite.to([this.lookProductMainImgContainer, this.lookDetailsWrapper], 0, {
        xPercent: 0,
        ease: Quad.easeInOut,
        onComplete: () => {
          const currentUrl = this.nextLookLink.getAttribute('href')
          const currentLocale = config.currentLocale.replace(/\//g, '')
          this.nextLookLink
            ? this.nextLookLink.setAttribute(
                'href',
                lookData ? `/${currentLocale}${lookData.url}` : ''
              )
            : ''
          this.previousLookLink.setAttribute('href', window.location.pathname)
          this.transformX = 0
          this.nextElementToLoad = lookData && lookData.nextElement ? lookData.nextElement : ''
          this.previousElementToLoad = this.previousLookElement
          this.previousLookImages = this.currentLookImages
          this.currentLookImages = this.nextLookImages
          this.nextLookImages = lookImages ? this.formatImages(JSON.stringify(lookImages)) : ''
          this.previousLookVideo = this.currentLookVideo
          this.currentLookVideo = this.nextLookVideo
          this.nextLookVideo = lookVideo
          this.previousLookCode = this.currentLookCode
          this.currentLookCode = this.nextLookCode
          this.nextLookCode = lookData ? lookData.passageNumber : ''
          this.previousLookElement = this.currentLookElement
          this.currentLookElement = this.nextLookElement
          this.nextLookElement = lookData ? lookData.code : ''
          this.previousLookId = this.currentLookId
          this.currentLookId = this.nextLookId
          this.nextLookId = lookData ? lookData.lookId : ''
          this.toggleSlidesAfterSwipe('next')
          this.correctLookData()
          this.updateTopProperty('next')
          this.updateAnalyticsData()
          window.history.pushState('', '', currentUrl)
          this.updateCardAttribute()
          // focusElement(this.currentCard)
          resolve()
        }
      })
    })
  }

  loadPrevLook(event) {
    event.target.tagName == 'A' ? event.preventDefault() : ''
    TweenLite.to([this.lookProductMainImgContainer, this.lookDetailsWrapper], 0.5, {
      xPercent: isLTR() ? 100 : -100,
      ease: Quad.easeInOut,
      onComplete: () => {
        const newLookImages = {}
        let newLookData = null
        let imageData = null
        let lookVideo = null
        document.body.classList.remove('stop-scrolling')
        pageTitle(
          config.popinLookPDP.lookCardNumber.replace('{0}', parseInt(this.previousLookCode))
        )
        if (this.previousElementToLoad !== null && this.previousElementToLoad !== '') {
          xhr
            .get(config.popinLookPDP.lookSwipeData.replace('{0}', this.previousElementToLoad))
            .then(data => {
              newLookData = data
              if (newLookData.lookImages !== null && newLookData.lookImages !== undefined) {
                newLookData.lookImages.map((elem, index) => {
                  elem.assetType = `${elem.assetType}_${index}`
                })
              }
              if (newLookData.lookImages === undefined && newLookData.allImages !== undefined) {
                imageData = newLookData.allImages
              } else if (
                newLookData.lookImages !== undefined &&
                newLookData.allImages !== undefined
              ) {
                imageData = newLookData.allImages.concat(newLookData.lookImages)
              } else if (newLookData.lookImages !== undefined) {
                imageData = newLookData.lookImages
              }

              if (newLookData.lookVideos !== undefined && newLookData.lookVideos.length !== 0) {
                lookVideo = this.configureVideo(newLookData.lookVideos, newLookData.passageNumber)
              } else if (newLookData.videos !== undefined && newLookData.videos.length !== 0) {
                lookVideo = this.configureVideo(newLookData.videos, newLookData.passageNumber)
              }
              if (imageData !== '' && imageData !== null) {
                const sortedImageData = imageData.sort((a, b) => a.galleryIndex - b.galleryIndex)
                sortedImageData.forEach(elem => {
                  if (elem.assetType !== 'VIDEO') newLookImages[elem.assetType] = elem.path
                })
              }
            })
            .then(() => {
              if (!this.allowSwipeLeft) {
                this.allowSwipeLeft = true
                this.nextCard.style.display = 'block'
                this.nextLookLink.style.display = 'block'
              }
              this.updatePreviousLookData(newLookData, newLookImages, lookVideo)
            })
            .then(() => {
              this.mobileLookTemplate(
                this.previousLookImages,
                this.previousLookCode,
                this.previousLookVideo,
                'prev'
              )
              this.updateLookCard(
                this.previousLookCode,
                this.previousLookElement,
                this.previousCard
              )
              this.setupLookVideo('prev')
              this.setZoomImages(this.currentLookCode)
              setTimeout(() => {
                this.previousCard.style.removeProperty('transform')
                this.nextCard.style.removeProperty('transform')
                this.navigationWrapper.style.display = 'block'
                this.allowSwipeRight = true
              }, 250)
            })
        } else {
          this.updatePreviousLookData(newLookData, newLookImages, lookVideo)
          this.removePreviousSlideData()
          this.removePreviousCard()
          this.setZoomImages(this.currentLookCode)
          this.setupLookVideo('prev')
          this.previousCard.style.removeProperty('transform')
          this.nextCard.style.removeProperty('transform')
          this.navigationWrapper.style.display = 'block'
        }
      }
    })
  }

  removePreviousCard() {
    this.previousCard.style.display = 'none'
    this.allowSwipeRight = false
    this.previousLookLink.style.display = 'none'
  }

  removePreviousSlideData() {
    while (this.prevLookSlide.firstChild) {
      this.prevLookSlide.removeChild(this.prevLookSlide.lastChild)
    }
  }

  updatePreviousLookData = async (lookData, lookImages, lookVideo) => {
    await new Promise(resolve => {
      TweenLite.to([this.lookProductMainImgContainer, this.lookDetailsWrapper], 0, {
        xPercent: 0,
        ease: Quad.easeInOut,
        onComplete: () => {
          const currentUrl = this.previousLookLink.getAttribute('href')
          const currentLocale = config.currentLocale.replace(/\//g, '')
          this.nextLookLink.setAttribute('href', window.location.pathname)
          this.previousLookLink
            ? this.previousLookLink.setAttribute(
                'href',
                lookData ? `/${currentLocale}${lookData.url}` : ''
              )
            : ''
          this.transformX = 0
          this.nextElementToLoad = this.nextLookElement
          this.previousElementToLoad =
            lookData && lookData.previousElement ? lookData.previousElement : ''
          this.nextLookImages = this.currentLookImages
          this.currentLookImages = this.previousLookImages
          this.previousLookImages = lookImages ? this.formatImages(JSON.stringify(lookImages)) : ''
          this.nextLookVideo = this.currentLookVideo
          this.currentLookVideo = this.previousLookVideo
          this.previousLookVideo = lookVideo
          this.nextLookCode = this.currentLookCode
          this.currentLookCode = this.previousLookCode
          this.previousLookCode = lookData ? lookData.passageNumber : ''
          this.nextLookElement = this.currentLookElement
          this.currentLookElement = this.previousLookElement
          this.previousLookElement = lookData ? lookData.code : ''
          this.nextLookId = this.currentLookId
          this.currentLookId = this.previousLookId
          this.previousLookId = lookData ? lookData.lookId : ''
          this.toggleSlidesAfterSwipe('prev')
          this.correctLookData()
          this.updateTopProperty('prev')
          this.updateAnalyticsData()
          window.history.pushState('', '', currentUrl)
          this.updateCardAttribute()
          focusElement(this.currentCard)
          resolve()
        }
      })
    })
  }

  toggleSlidesAfterSwipe(slidePos) {
    const newSwipedSlide = select(`.look-slideshow__${slidePos}`)
    this.navigationWrapper.style.display = 'none'
    if (newSwipedSlide !== undefined && newSwipedSlide !== null) {
      if (slidePos == 'next') {
        this.nextLookSlide.style.left = 0
        this.prevLookSlide.classList.remove('look-slideshow__prev')
        this.currentLookSlide.classList.remove('look-slideshow__current')
        this.currentLookSlide.classList.add('look-slideshow__prev')
        this.nextLookSlide.classList.remove('look-slideshow__next')
        this.nextLookSlide.classList.add('look-slideshow__current')
        this.prevLookSlide.classList.add('look-slideshow__next')
      } else {
        this.prevLookSlide.style.right = 0
        this.nextLookSlide.classList.remove('look-slideshow__next')
        this.currentLookSlide.classList.remove('look-slideshow__current')
        this.currentLookSlide.classList.add('look-slideshow__next')
        this.prevLookSlide.classList.remove('look-slideshow__prev')
        this.prevLookSlide.classList.add('look-slideshow__current')
        this.nextLookSlide.classList.add('look-slideshow__prev')
      }
      this.orderLookSlide()
      slidePos == 'next'
        ? newSwipedSlide.style.removeProperty('left')
        : newSwipedSlide.style.removeProperty('right')
      this.currentLookSlide.removeAttribute('aria-hidden')
      selectAll('button', this.currentLookSlide).map(item => item.removeAttribute('tabindex'))
      this.prevLookSlide.setAttribute('aria-hidden', true)
      this.nextLookSlide.setAttribute('aria-hidden', true)
      this.toggleProductCardsAfterSwipe(slidePos)
      selectAll('button', select('.look-slideshow__prev')).map(item => item.setAttribute('tabindex', "-1"))
      selectAll('button', select('.look-slideshow__next')).map(item => item.setAttribute('tabindex', "-1"))
    }
  }

  orderLookSlide() {
    this.currentLookSlide = select('.look-slideshow__current')
    this.prevLookSlide = select('.look-slideshow__prev')
    this.nextLookSlide = select('.look-slideshow__next')
    this.lookSlideShowWrapper.appendChild(this.prevLookSlide)
    this.lookSlideShowWrapper.appendChild(this.nextLookSlide)
  }

  updateTopProperty = slidePos => {
    window.scrollTo(0, 0)
    this.currentLookSlide.style.removeProperty('top')
    slidePos == 'next'
      ? (this.nextLookSlide.style.top = '0px')
      : (this.prevLookSlide.style.top = '0px')
  }

  toggleProductCardsAfterSwipe(slidePos) {
    const newProductCard = select(`.look-page__card-wrapper.${slidePos}-card`)
    const allProductCard = getCollection('.look-page__card-wrapper')
    if (newProductCard !== null && newProductCard !== undefined) {
      this.changeProductCardHeading(slidePos)
      if (slidePos == 'next') {
        newProductCard.style.left = 0
        this.previousCard.classList.remove('prev-card')
        this.currentCard.classList.remove('current-card')
        this.currentCard.classList.add('prev-card')
        this.nextCard.classList.remove('next-card')
        this.nextCard.classList.add('current-card')
        this.previousCard.classList.add('next-card')
      } else {
        newProductCard.style.right = 0
        this.nextCard.classList.remove('next-card')
        this.currentCard.classList.remove('current-card')
        this.currentCard.classList.add('next-card')
        this.previousCard.classList.remove('prev-card')
        this.previousCard.classList.add('current-card')
        this.nextCard.classList.add('prev-card')
      }
      this.orderProductCards(slidePos)
      slidePos == 'next'
        ? newProductCard.style.removeProperty('left')
        : newProductCard.style.removeProperty('right')
    }
  }

  changeProductCardHeading(slidePos) {
    const newProductCard = select(`.look-page__card-wrapper.${slidePos}-card`)
    const currentProductCard = select('.look-page__card-wrapper.current-card')
    const cardArray = [newProductCard, currentProductCard]
    cardArray.forEach((card, index) => {
      const productCardContainer = select('.look-description__card-container', card)
      const productCardData = select('.look-description__content', card)
      let headingElem = null
      if (index === 0) {
        headingElem = document.createElement('H1')
      } else {
        headingElem = document.createElement('DIV')
        headingElem.setAttribute('heading', 'role')
        headingElem.setAttribute('aria-level', 1)
      }
      headingElem.classList.add('look-description__content')
      this.lookPageWithProducts ? headingElem.classList.add('link-margin-bottom') : ''
      headingElem.setAttribute('id', productCardData.getAttribute('id'))
      headingElem.innerHTML = productCardData.innerHTML
      productCardContainer.removeChild(productCardData)
      productCardContainer.insertBefore(
        headingElem,
        select('.look-description__right', productCardContainer)
      )
    })
  }

  orderProductCards(slidePos) {
    this.currentCard = select('.look-page__card-wrapper.current-card')
    this.previousCard = select('.look-page__card-wrapper.prev-card')
    this.nextCard = select('.look-page__card-wrapper.next-card')
    this.navigationWrapper = select('.look-page__look-navigation')
    this.lookDetailsWrapper.appendChild(this.previousCard)
    this.lookDetailsWrapper.appendChild(this.nextCard)
    this.lookDetailsWrapper.appendChild(this.navigationWrapper)
    this.calcPdpCardPosition()
  }

  updateCardAttribute() {
    const lookCards = getCollection('.look-page__card-wrapper')
    lookCards.length > 0
      ? lookCards.forEach(card => {
        if (card.classList.contains('current-card')) {
          card.removeAttribute('aria-hidden')
          card.removeAttribute('tabindex')
          card.querySelectorAll('button').length
              ? card.querySelectorAll('button').forEach(btn => {
                btn.removeAttribute('tabindex')
              })
              : ''
          card.querySelectorAll('a').length > 0
              ? card.querySelectorAll('a').forEach(anchor => {
                anchor.removeAttribute('tabindex')
              })
              : ''
        } else {
          card.setAttribute('aria-hidden', true)
          card.setAttribute('tabindex', -1)
          card.querySelectorAll('button').length > 0
              ? card.querySelectorAll('button').forEach(btn => {
                btn.setAttribute('tabindex', -1)
              })
              : ''
          card.querySelectorAll('a').length > 0
              ? card.querySelectorAll('a').forEach(anchor => {
                anchor.setAttribute('tabindex', -1)
              })
              : ''
        }
      })
      : ''
  }

  setupLookVideo(slidePos) {
    if (slidePos == 'next') {
      if (this.nextLookVideo !== null && this.nextLookVideo !== '') {
        init(this.nextLookSlide)
      }
    } else if (this.previousLookVideo !== null && this.previousLookVideo !== '') {
      init(this.prevLookSlide)
    }
    this.lookVideoContainer = select('.look-page__video-wrapper', this.currentLookSlide)
    if (this.lookVideoContainer !== null && this.lookVideoContainer !== '') {
      this.lookVideoContainer.addEventListener('keypress', this.onButtonVideoPlayPause, true)
      this.lookVideoContainer.addEventListener('click', this.onButtonVideoPlayPause, true)
    }
  }

  correctLookData() {
    const currentLookMedia = getCollection('li.look-page__img-wrapper', this.currentLookSlide)
    if (currentLookMedia !== null && currentLookMedia.length !== 0) {
      currentLookMedia.forEach(item => {
        item.style.display == 'none' ? item.style.removeProperty('display') : ''
      })
      const maxImageToDisplay = currentLookMedia.length
      const nextLookMedia = getCollection('li.look-page__img-wrapper', this.nextLookSlide)
      const previousLookMedia = getCollection('li.look-page__img-wrapper', this.prevLookSlide)
      if (nextLookMedia.length > maxImageToDisplay) {
        for (let med = maxImageToDisplay; med < nextLookMedia.length; med++) {
          nextLookMedia[med].style.display = 'none'
        }
      }
      if (previousLookMedia.length > maxImageToDisplay) {
        for (let med = maxImageToDisplay; med < previousLookMedia.length; med++) {
          previousLookMedia[med].style.display = 'none'
        }
      }
    }
  }

  updateAnalyticsData() {
    const dataLayerObj = Object.assign(cloneDeep(dataLayerGA.pageLoad))
    dataLayerObj.lookId = this.currentLookId
    dataLayerObj.page_name = document.title
    if (isMobile()) {
      dataLayerObj.event = 'virtualpageview'
    }
    window.document.DLKit.push('on-tracking', dataLayerObj)
  }

  mobileLookTemplate(lookImages, lookCode, lookVideo, slidePos) {
    const html = document.querySelector(`.look-slideshow__${slidePos}`)
    html.dataset.looknumber = parseInt(lookCode)
    let tempList = ''
    let imageAlt
    let videoIndex = 0
    const videoAriaLabel = config.popinLookPDP.lookCategory
      .replace('{0}', 1)
      .replace('{1}', parseInt(lookCode))
    const zoomableImage = true
    if (lookImages && lookImages.length !== 0) {
      for (let index = 0; index < lookImages.length; index++) {
        imageAlt = config.popinLookPDP.lookImageAltText
          .replace('{0}', index + 1)
          .replace('{1}', parseInt(lookCode))
        // zoomableImage = (lookImages[index].assetType == 'PACKSHOT_DEFAULT' || lookImages[index].assetType == 'PACKSHOT_ALTERNATE' || lookImages[index].assetType == 'PACKSHOT_OTHER') ? true : false
        if (zoomableImage) {
          const defaultSrcFallback = `w_${window.config.cloudinary.defaultSrcFallBack}`
          const zoomSrcFallback = `w_${window.config.cloudinary.srcFallback}`
          const zoomImageSrc = lookImages[index].replace(defaultSrcFallback, zoomSrcFallback)
          const zoomImageAlt = config.popinLookPDP.lookZoomImageAlt.replace('{0}', imageAlt)
          tempList =
            `${tempList
            }<li class="look-page__img-wrapper look-page__img-position" id="look-details-${
            videoIndex
            }">
				<button class="look-page__zoom-btn js-look-image-zoom" type="button" data-zoomable="true" tabindex="-1" data-test="imgProduct_PDP">
					<img class="lazyautosizes lazyloaded" src="${
            lookImages[index]
            }" alt="${
            zoomImageAlt
            }">
					<img class="img-zoom-js is-hidden" async-src="${
            zoomImageSrc
            }"
						src="" alt="${
            imageAlt
            }"
		        		/>
		        	</button>
		        </li>`
        } else {
          tempList =
            `${tempList
            }<li class="look-page__img-wrapper look-page__img-position" id="look-details-${
            videoIndex
            }">
					<img class="lazyautosizes lazyloaded" src="${
            lookImages[index]
            }" alt="${
            imageAlt
            }">
		        </li>`
        }
        if (index == 0 && lookVideo !== null) {
          videoIndex = index + 1
          tempList =
            `${tempList
            }<li class="look-page__img-wrapper look-page__img-position look-page__video-wrapper" id="look-details-${
            videoIndex
            }">${
            lookVideo
            }<button type="button" class="video-container" data-label-pause="Pause Video" tabindex="-1" data-label-play="Play Video">
		       		 	<svg data-test="" class="icon is-large pause-icon" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
		       		 		<use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg?#video-pause"></use>
		       		 	</svg>
		       		 	<svg data-test="" class="icon is-large play-icon" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
		       		 		<use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg?#video-play"></use>
		       		 	</svg>
					<span class="is-sr-only js-video-status">${
            videoAriaLabel
            }</span>
		       		 	</button>
		       		 </li>`
        }
        videoIndex += 1
      }
    } else if (lookVideo !== null) {
      tempList =
        `${tempList
        }<li class="look-page__img-wrapper look-page__img-position look-page__video-wrapper" id="look-details-${
        videoIndex
        }">${
        lookVideo
        }<button type="button" class="video-container" data-label-pause="Pause Video" tabindex="-1" data-label-play="Play Video">
     		 	<svg data-test="" class="icon is-large pause-icon" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
              		<use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg?#video-pause"></use>
            	</svg>
                <svg data-test="" class="icon is-large play-icon" focusable="false" viewBox="0 0 1 1" aria-hidden="true">
              		<use xlink:href="/_ui/responsive/theme-onechanel/assets/icons.svg?#video-play"></use>
            	</svg>
			<span class="is-sr-only js-video-status">${
        videoAriaLabel
        }</span>
     		 	</button>
     		 </li>`
    }
    html.innerHTML = tempList
  }

  updateLookCard(newLookCode, newLookElement, cardToUpdate) {
    const newHeadingId = `look-details__${newLookCode}`
    const newHeadingText = config.popinLookPDP.lookCardNumber.replace('{0}', parseInt(newLookCode))
    const wishlistBtn = select('.look-description__wishlist button', cardToUpdate)
    const viewDetailsCta = select('.look-description__viewdetails button', cardToUpdate)
    select('.look-description__content', cardToUpdate).setAttribute('id', newHeadingId)
    select('.look-description__number', cardToUpdate).innerText = newHeadingText
    if (wishlistBtn !== null) {
      wishlistBtn.dataset.wishlist = newLookElement
      wishlistBtn.dataset.trackWishlist = {
        eventAction: newHeadingText,
        eventLabel: newLookElement
      }
      CacheIndex.rePublishPageLoadData(PAGELOAD_WISHLIST)
      if (this.wishListData !== null && this.wishListData.indexOf(newLookElement) !== -1) {
        wishlistBtn.classList.add('is-active')
      } else {
        wishlistBtn.classList.remove('is-active')
      }
      wishlistBtn.setAttribute('aria-describedby', newHeadingId)
    }
    if (viewDetailsCta) {
      const codeData = JSON.parse(viewDetailsCta.dataset.code)
      const newLookCategory = codeData.lookCategory
      viewDetailsCta.setAttribute('aria-describedby', newHeadingId)
      viewDetailsCta.dataset.code = JSON.stringify({
        lookCode: newLookElement,
        lookCategory: newLookCategory
      })
    }
  }

  focusMediaOnClick = e => {
    const elementToFocus = e.currentTarget.dataset.imageid
    const focusedElementRef = select(`#${elementToFocus}`)
    if ((e.type === 'click' || e.type === 'keyup') && e.sourceCapabilities) {
      const elemScrollTop = focusedElementRef.offsetTop + LOOK_STICKY_SIZES.large
      if (e.which === 1 && e.type == 'click') {
        elementToFocus ? window.scrollTo({ left: 0, top: elemScrollTop, behavior: 'smooth' }) : ''
      } else if (e.which === 13) {
        elementToFocus ? window.scrollTo({ left: 0, top: elemScrollTop, behavior: 'smooth' }) : ''
        if (focusedElementRef.classList.contains('look-page__video-wrapper')) {
          focusElement(select('.video-container', select(`#${elementToFocus}`)))
        } else {
          setTimeout(() => {
            focusElement(select(`#${elementToFocus} img`))
          }, 1000)
        }
      } else if (e.which === 32) {
        const isImageElement = !focusedElementRef.classList.contains('look-page__video-wrapper')
        if (isImageElement) {
          window.scrollTo({ left: 0, top: focusedElementRef.offsetTop, behavior: 'smooth' })
          focusElement(select(`#${elementToFocus} img`))
        } else {
          focusElement(select('.video-container', focusedElementRef))
        }
      }
    }
  }

  toggleMobileLookCards = () => {
    const lookImages = getCollection('li', select('.look-page__images-container'))
    if (lookImages.length > 0) {
      for (let index = 0; index < lookImages.length; index++) {
        if (lookImages[index].querySelector('img')) {
          lookImages[index].querySelector('img').setAttribute('tabindex', -1)
        }
      }
    }
  }

  onBreakpointChange = () => {
    this.initSyncControlsDots({
      updateOffset: true
    })
    this.initSyncColsTitle({
      updateOffset: true
    })
    this.initScrollSpyMobile()
    this.resetImageStyle()
    this.showHideButtonZoom()
    this.setLookimgSize()
  }

  // US: ONE-17555
  onButtonVideoPlayPause = e => {
    if (e.type === 'click' && e.sourceCapabilities === null) {
      // click unknown: no mouse click and no touch
    } else if (e.type === 'keyup' || e.type === 'click') {
      e.stopPropagation()
      const videoElement = this.lookVideoContainer.querySelector('video')
      if (videoElement) {
        if (
          (e.type === 'keyup' && e.which === 32) ||
          (e.type === 'keyup' && e.which === 13) ||
          e.type === 'click'
        ) {
          if (videoElement.paused) {
            videoElement.play()
          } else {
            videoElement.pause()
          }
        }
        this.togglePlayPauseIcon()
      }
    }
  }

  handlePlayPauseButtons = () => {
    const videoElement = this.lookVideoContainer.querySelector('video')
    if(videoElement) {
      videoElement.addEventListener('play',() => this.togglePlayPauseIcon())
      videoElement.addEventListener('pause',() => this.togglePlayPauseIcon())
    }
  }

  togglePlayPauseIcon() {
    if (this.lookVideoContainer) {
      const videoButtonRef = select('.look-page__video-wrapper .video-container')
      if (videoButtonRef) {
        const playLabel = videoButtonRef.getAttribute('data-label-play')
        const pauseLabel = videoButtonRef.getAttribute('data-label-pause')
        const spanElem = videoButtonRef.querySelector('.js-video-status')
        const playButtonIcon = videoButtonRef.querySelector('.play-icon')
        const pauseButtonIcon = videoButtonRef.querySelector('.pause-icon')
        const videoElement = this.lookVideoContainer
          ? this.lookVideoContainer.querySelector('video')
          : ''
        if (videoElement && spanElem) {
          if (videoElement.paused) {
            spanElem.innerHTML = spanElem.innerHTML.replace(pauseLabel, playLabel)
            playButtonIcon.style.display = 'block'
            pauseButtonIcon.style.display = 'none'
          } else {
            spanElem.innerHTML = spanElem.innerHTML.replace(playLabel, pauseLabel)
            playButtonIcon.style.display = 'none'
            pauseButtonIcon.style.display = 'block'
          }
        }
      }
    }
  }

  handleControlsKeyUp = e => {
    if (e.which === 13) {
      const element = e.target
      const elementToGo = select(element.hash, this.container)
      if (this.lookPageWithProducts) {
        const elementToFocus = select('.look-page__details-wrapper-inner', elementToGo)
          ? select('.look-page__details-wrapper-inner', elementToGo)
          : elementToGo
        Array.from(selectAll('.look-page__details-wrapper-inner', this.container)).forEach(el =>
          el.removeAttribute('tabindex')
        )
        setTimeout(() => {
          elementToFocus.setAttribute('tabindex', -1)
          focusElement(elementToFocus)
        }, 250)
      } else if (this.lookPageWithImages) {
        const elementToFocus = elementToGo
        setTimeout(() => {
          focusElement(elementToFocus)
        }, 250)
      }
    }
  }

  initSyncControlsDots = (options = {}) => {
    if (!this.lookPageWithImages && !this.lookPageWithProducts) {
      return
    }

    // in mobile destroy scrollSpyDots || in tablet if we are in products destroy scrollSpyDots
    if ((!isTabletScreen() && !isWideScreen()) || (isTabletScreen() && this.lookPageWithProducts)) {
      if (this.syncColsDots) {
        this.syncColsDots.destroy()
      }
      if (this.scrollSpyDots) {
        this.scrollSpyDots.destroy()
      }
      return
    }

    if (!this.syncColsDots) {
      this.syncColsDots = new SyncCols(
        this.pageControlsWrapper,
        this.container,
        this.calculateHeight(),
      )
    }

    if (!this.scrollSpyDots) {
      let scrollSpyElement
      let customElements = []
      let onUpdate = () => {}
      if (!this.enableNewPdp) {
        if (this.lookPageWithImages) {
          scrollSpyElement = select('.look-page__alternative-imgs-container')
          customElements = [select('.look-page__main-img')]
        } else if (this.lookPageWithProducts) {
          scrollSpyElement = select('.look-page__details-wrapper')
          customElements = select('.look-page__alternative-imgs-container')
            ? [select('.look-page__alternative-imgs-container')]
            : []
          onUpdate = this.animateImage
        }
      } else {
        scrollSpyElement = select('.look-page__images-container')
      }

      this.scrollSpyDots = new ScrollSpy(scrollSpyElement, {
        stickyOffset: this.calculateHeight(),
        changeThreshold: '.5',
        onChange: this.updateScrollSpyDots,
        onUpdate,
        customElements
      })
    }

    if (!this.syncColsDots.isInited()) {
      this.syncColsDots.init()
    }

    if (!this.scrollSpyDots.isInited()) {
      this.scrollSpyDots.init()
    }

    if (options.updateOffset) {
      this.syncColsDots.updateStickyOffset(this.calculateHeight())
    }
  }

  initSyncColsTitle = (options = {}) => {
    if (!this.lookPageWithImages && !this.enableNewPdp) {
      return
    }
    // in mobile destroy ScrollSpyTitle (page with images)
    if (!isWideScreen()) {
      if (this.syncColsTitle) {
        this.syncColsTitle.destroy()
      }
      return
    }

    if (!this.syncColsTitle) {
      if (this.lookPageWithImages && !this.enableNewPdp) {
        this.syncColsTitle = new SyncCols(
          this.pageTitleWrapper,
          this.container,
          this.calculateHeight()
        )
      } else if (this.enableNewPdp) {
        this.syncColsTitle = new SyncCols(
          select('.look-page__details-wrapper-fixed'),
          this.container,
          this.calculateHeight()
        )
      }
    }
    if (!this.syncColsTitle.isInited()) {
      this.syncColsTitle.init()
    }

    if (options.updateOffset) {
      this.syncColsTitle.updateStickyOffset(this.calculateHeight())
    }
  }

  initScrollSpyMobile = (options = {}) => {
    const scrollSpyMobileEl = select('.look-page__details-wrapper')
    if (!this.lookPageWithProducts || this.enableNewPdp) {
      return
    }
    // in desktop destroy scrollSpyMobile (page with products)
    if (isWideScreen()) {
      if (this.scrollSpyMobile) {
        this.scrollSpyMobile.destroy()
      }
      return
    }

    if (!this.scrollSpyMobile) {
      this.scrollSpyMobile = new ScrollSpy(scrollSpyMobileEl, {
        stickyOffset: this.calculateHeight(),
        changeThreshold: '.5',
        onUpdate: this.animateImageOnMobile
      })
    }
    if (!this.scrollSpyMobile.isInited()) {
      this.scrollSpyMobile.init()
    }
  }

  animateImage = (scrollPosition, progressToTop) => {
    const containerItemsHeight = this.pageTitleWrapper.firstElementChild.offsetHeight
    const oldState = this.currentAnimationState
    const additonalImageList = getCollection('.look-additional-images')
    this.isTranslate = true
    if (progressToTop === 0) {
      this.currentAnimationState = 'static'
    } else if (progressToTop < 1) {
      this.currentAnimationState = 'fixed'
    } else {
      this.isTranslate = false
    }

    if (this.currentAnimationState !== oldState) {
      this.setImagePositioning(this.lookMainZoomableImage, this.currentAnimationState)
    }

    // hide the overflow zoomed image in case of additional images
    if (additonalImageList.length > 0 && this.lookPageWithProducts) {
      if (
        window.scrollY + window.innerHeight >=
        this.lookProductMainImgContainer.offsetHeight + this.calculateHeight()
      ) {
        this.lookProductMainImgContainer.style.overflow = 'hidden'
      } else {
        this.lookProductMainImgContainer.style.overflow = 'unset'
      }
    }

    // Zoom on image on the first item
    if (window.scrollY < containerItemsHeight) {
      this.scaleImage(containerItemsHeight)
      this.fadeScrollArrow(containerItemsHeight)
    } else if (this.isTranslate) {
      this.translateImage(scrollPosition)
    } else {
      this.reverseTranslate()
    }
  }

  fadeScrollArrow = containerItemsHeight => {
    const scrollDownElem = select('.scroll-to-explore', document.body)
    const opacityScale = (containerItemsHeight - window.scrollY) / containerItemsHeight - 0.2
    new TimelineLite().to(scrollDownElem, 0.3, { opacity: opacityScale })
  }

  // TODO: this function should be binded on scroll and after finishing css
  animateImageOnMobile = () => {
    const diffHeight = window.scrollY + window.innerHeight - this.calculateHeight()
    const mainPosition = diffHeight / this.mobileContentMainImgHeight
    const imagePosition = mainPosition * 100
    const diffH = this.lookMainZoomableImage.clientHeight - this.calculateHeight()
    const imgPosition = (window.scrollY * 100) / diffH

    if (window.scrollY < 1) {
      if (this.lookMainZoomableImage.classList.contains('zoom-in')) {
        this.oldMobileActiveProduct = null
        this.currentMobileActiveProduct = null
        this.lookMainZoomableImage.classList.remove('zoom-in')
      }
      this.lookItemsLi.forEach(li => li.classList.remove('is-visible'))
      this.lookItemsLi[0].classList.add('is-visible')
    } else {
      if (!this.lookMainZoomableImage.classList.contains('zoom-in')) {
        this.lookMainZoomableImage.classList.add('zoom-in')
      }
      if (mainPosition < 1) {
        if (imgPosition < this.lastMobileProductMinMax.mobileMax) {
          for (const lookItem of this.lookItems) {
            const lookElement = lookItem.closest('li')
            const lookMinMax = select('.look-description', lookElement).dataset
            if (imgPosition <= lookMinMax.mobileMax) {
              this.oldMobileActiveProduct = this.currentMobileActiveProduct
              this.currentMobileActiveProduct = lookElement
              break
            }
          }
        } else {
          this.oldMobileActiveProduct = this.currentMobileActiveProduct
          this.currentMobileActiveProduct = this.lastLookProduct
        }

        if (
          this.currentMobileActiveProduct !== this.oldMobileActiveProduct ||
          this.isLastElementMobileStatic
        ) {
          this.isLastElementMobileStatic = false
          this.oldMobileActiveProduct = this.currentMobileActiveProduct

          this.lookItemsLi.forEach(li => li.classList.remove('is-visible'))
          this.lastLookProduct.classList.remove('static-last')
          this.currentMobileActiveProduct.classList.add('is-visible')
        }

        // focus title of active element
      } else if (!this.isLastElementMobileStatic) {
        this.isLastElementMobileStatic = true
        const zoomedImageSize = this.lookMainZoomableImage.getBoundingClientRect().height
        const lookImageWrapper = select('.look-page__multiple-products', this.container)
        this.lookItemsLi.forEach(li => li.classList.remove('is-visible'))
        this.lastLookProduct.classList.remove('is-visible')
        this.lastLookProduct.classList.add('static-last')
        TweenLite.set(lookImageWrapper, { height: zoomedImageSize })
      }
    }
  }

  transformLookItemsForMobile = () => {
    const MIN_GAP = 5
    const MAX_GAP = MIN_GAP * 2
    if (isMobileScreen() || isTabletScreen()) {
      const rversedLooks = Array.from(this.lookItems).reverse()
      rversedLooks.forEach((el, i) => {
        const currentLook = select('.look-description', el.closest('li'))
        if (i !== 0) {
          const previousLook = select('.look-description', rversedLooks[i - 1].closest('li'))
          const previousMax = previousLook.dataset.mobileMax
            ? previousLook.dataset.mobileMax
            : previousLook.dataset.max
          const currentMax = currentLook.dataset.mobileMax
            ? currentLook.dataset.mobileMax
            : currentLook.dataset.max
          let newMax = 0
          if (currentMax) {
            newMax = previousMax - currentMax > MAX_GAP ? currentMax + MIN_GAP : currentMax
          } else {
            newMax = previousMax - MIN_GAP
          }
          // test for first element (last element in reversed)
          newMax = newMax < 0 ? 0 : newMax
          currentLook.dataset.mobileMax = newMax
        } else {
          currentLook.dataset.mobileMax = currentLook.dataset.max
        }
      })

      rversedLooks.forEach((el, i) => {
        const currentLook = select('.look-description', el.closest('li'))
        if (i !== 0) {
          const previousLook = select('.look-description', rversedLooks[i - 1].closest('li'))
          const previousMax = previousLook.dataset.mobileMax
            ? previousLook.dataset.mobileMax
            : previousLook.dataset.max
          const currentMax = currentLook.dataset.mobileMax
            ? currentLook.dataset.mobileMax
            : currentLook.dataset.max
          let newMax = 0
          if (currentMax) {
            newMax = previousMax - currentMax >= MIN_GAP ? currentMax : previousMax - MIN_GAP
          } else {
            newMax = previousMax - MIN_GAP
          }
          // test for first element (last element in reversed)
          newMax = newMax < 0 ? 0 : newMax
          currentLook.dataset.mobileMax = newMax
        } else {
          currentLook.dataset.mobileMax = currentLook.dataset.max
        }
      })
    }
  }

  preventDefaultScroll = e => {
    e = e || window.event
    if (e.preventDefault) {
      e.preventDefault()
    }
    if (e.stopPropagation) {
      e.stopPropagation()
    }
    e.returnValue = false
  }

  disableScroll = timeout => {
    if (window.addEventListener) {
      window.addEventListener('scroll', this.preventDefaultScroll, false)
    }
    window.onwheel = this.preventDefaultScroll // modern standard
    document.onmousewheel = this.preventDefaultScroll // older browsers
    window.onmousewheel = this.preventDefaultScroll // older browsers
    window.ontouchmove = this.preventDefaultScroll // mobile
    window.onscroll = this.preventDefaultScroll
    this.timeoutScrollMobile = setTimeout(this.enableScroll, timeout)
  }

  enableScroll = () => {
    if (window.removeEventListener) {
      window.removeEventListener('scroll', this.preventDefaultScroll, false)
    }
    window.onwheel = null
    document.onmousewheel = null
    window.onmousewheel = null
    window.ontouchmove = null
    this.timeoutScrollMobile = null
  }

  // ONE-1260 - focus order mobile|tablet
  goToActiveLookElement = (e, isDataMaxSet, lastLookItemId, lastLookItemEl) => {
    if (isWideScreen()) {
      const containerItemsHeight = this.pageTitleWrapper.firstElementChild.offsetHeight
      window.scroll(0, containerItemsHeight - 1)
      return
    }

    const el = e.delegateTarget
    const goTo = el.dataset.goTo
    const imgHeight = this.lookMainZoomableImage.clientHeight - this.calculateHeight()
    const currentId = el.closest('li').id
    const currentElement = select(`li#${currentId}`, this.pageTitleWrapper)
    const targetElementId = currentId.replace(
      /\d+$/,
      parseInt(currentId.split('-').pop(), 10) + (goTo === 'next' || !goTo ? 1 : -1)
    )
    const targetElement = select(`li#${targetElementId}`, this.pageTitleWrapper)
    const targetElementLook = select('.look-description', targetElement)
    const newMax = parseInt(targetElementLook.dataset[isDataMaxSet ? 'max' : 'mobileMax'], 10)
    const targetImagePosition = parseInt((newMax * imgHeight) / 100)
    const targetDescriptionHead = select('.look-description__head', targetElementLook)

    window.scrollTo({
      top: targetImagePosition
    })

    window.addEventListener('scroll', () => scrollBehaviour())

    let flag = true
    const scrollBehaviour = () => {
      const currentScrollOffset = window.pageYOffset || document.documentElement.scrollTop
      // Scroll reach the target
      if (currentScrollOffset === targetImagePosition && flag) {
        if (goTo) {
          if (targetElementId !== lastLookItemId) {
            lastLookItemEl.classList.remove('is-visible', 'static-last')
            currentElement.classList.remove('is-visible')
            targetElement.classList.add('is-visible')
          } else {
            lastLookItemEl.classList.add('is-visible', 'static-last')
          }
        }

        flag = false
        focusElement(targetDescriptionHead)
      }
    }
  }

  setLookimgSize = state => {
    if (isWideScreen()) {
      // set default width
      this.lookMainZoomableImage.style.width = '390px'

      const pageHeight = window.innerHeight - this.calculateHeight(), // page height without header and nav
        lookWidth =
          Math.round(pageHeight - pageHeight * (50 / 100)) > 390
            ? `${Math.round(pageHeight - pageHeight * (50 / 100))}px`
            : '390px' // calculate page width, 70% of the height

      if (!this.initialWidth) {
        this.initialWidth = lookWidth
      }

      this.lookMainZoomableImage.style.width = `${lookWidth}`
      document.querySelector('.look-page__img-zoom').style.maxWidth = `${lookWidth}`

      if (state === 'static') {
        this.lookMainZoomableImage.style.width = `${this.initialWidth}px`
      }
    } else {
      document.querySelector('.look-page__img-zoom').style.maxWidth = 'initial'
    }
  }

  setImagePositioning = (element, state, lookWidth) => {
    let style

    this.setLookimgSize()

    switch (state) {
      case 'static':
        style = {
          position: 'static',
          bottom: 'auto'
        }
        break
      case 'fixed':
        style = {
          position: 'fixed',
          bottom: 'auto',
          transformOrigin: '50% 0%',
          width: lookWidth
        }
        break
      default:
        style = {
          position: 'absolute',
          top: 'auto',
          bottom: '-140px',
          y: 0,
          transformOrigin: '50% 50%'
        }
    }
    TweenLite.set(element, style)
  }

  resetImageStyle = () => {
    if (this.lookPageWithProducts && (isTabletScreen() || isMobileScreen)) {
      TweenLite.set(this.lookMainZoomableImage, { clearProps: 'all' })
    }
  }

  // translate image on scroll
  translateImage = scrollPosition => {
    if (this.visibleDiff !== null) {
      this.restoreReversedParameters()
    }
    if (scrollPosition && scrollPosition < 1) {
      const scrollCoef = scrollPosition * 10
      const lookItemMinMax = this.lookItemsMinMax.find(element => scrollCoef * 10 < element.max)

      this.onItemActivated({
        scrollCoef,
        lookItem: lookItemMinMax,
        lookItems: this.lookItemsMinMax
      })
    }
  }

  // Manage the scroll animation with item active
  onItemActivated = data => {
    const coefFix = parseFloat(data.scrollCoef.toFixed(1)) + 0.1
    if (data.lookItem && data.lookItem.max !== '') {
      const scrollDirection = scrollDirectionDetector()
      const coefScale = (data.scrollCoef * coefFix) / data.lookItem.max
      if (scrollDirection) {
        this.currentCoefScale =
          this.currentCoefScale < coefScale
            ? coefScale
            : this.currentCoefScale - Math.abs(coefScale - this.currentCoefScale)
      } else if (!scrollDirection) {
        this.currentCoefScale =
          this.currentCoefScale > coefScale
            ? coefScale
            : this.currentCoefScale + Math.abs(coefScale - this.currentCoefScale)
      }
      TweenLite.set(this.lookMainZoomableImage, {
        y: `-${this.currentCoefScale * 100}%`,
        scale: 1.85
      })
    }
  }

  reverseTranslate = () => {
    if (this.visibleDiff === null) {
      this.visibleDiff = Math.floor(
        parseFloat(window.getComputedStyle(this.lookMainZoomableImage).bottom.replace('px', ''))
      )
      if (this.visibleDiff < 0) {
        this.visibleDiff = 0
      }
    }

    const style = {
      position: 'absolute',
      top: 'auto',
      bottom: `${this.visibleDiff}px`,
      y: 0,
      transformOrigin: '50% 0%'
    }

    if (this.visibleDiff >= 0) {
      TweenLite.set(this.lookMainZoomableImage, style)
    }
  }

  restoreReversedParameters = () => {
    this.visibleDiff = null
    const style = {
      position: 'fixed',
      bottom: 'auto'
    }
    TweenLite.set(this.lookMainZoomableImage, style)
  }

  scaleImage = containerItemsHeight => {
    let percent = (window.scrollY * 100) / containerItemsHeight
    if (percent < 10) {
      percent = `0${percent}`
    }
    const scale = `1.${percent}`
    TweenLite.set(this.lookMainZoomableImage, { scale, yPercent: 0 })
  }

  // Dots Update on Scroll
  updateScrollSpyDots = activeIndex => {
    this.indexControlImage = activeIndex

    if (this.controlDots) {
      this.controlDots.forEach(($bullet, index) => {
        const $bulletLink = select('a', $bullet)
        if ($bullet.classList.contains('is-active') && activeIndex !== index) {
          $bullet.classList.remove('is-active')
          if ($bulletLink) $bulletLink.removeAttribute('aria-current')
        } else if (activeIndex === index) {
          $bullet.classList.add('is-active')
          if ($bulletLink) $bulletLink.setAttribute('aria-current', true)
        }
      })
    }
  }

  calculateHeight = () => {
    let height = HEADER_SIZES.small_collapsed + LOOK_STICKY_SIZES.small

    if (isTabletScreen()) {
      height = HEADER_SIZES.medium_collapsed + LOOK_STICKY_SIZES.medium
    } else if (isWideScreen() && select('body').classList.contains('is-body-narrow')) {
      height = HEADER_SIZES.large_collapsed + LOOK_STICKY_SIZES.large
    } else if (isWideScreen()) {
      height = HEADER_SIZES.large_expanded + LOOK_STICKY_SIZES.large
    } else {
      height = HEADER_SIZES.small_collapsed + LOOK_STICKY_SIZES.small
    }

    return height
  }

  setZoomImages = lookCode => {
    const currentLookSlides = select('.look-slideshow__current')
    const zoomableImages = selectAll('.js-look-image-zoom', currentLookSlides)
    let lookNumber = null
    if (lookCode == undefined || lookCode == '') {
      lookNumber = currentLookSlides.dataset.looknumber
        ? parseInt(currentLookSlides.dataset.looknumber)
        : undefined
    } else {
      lookNumber = parseInt(lookCode)
    }
    let ariaLabelToSet = ''
    if (lookNumber !== undefined && lookNumber !== '') {
      ariaLabelToSet = config.popinLookPDP.dialogBtnLabel.replace('{0}', lookNumber)
    }
    const options = {
      contentClass: '.lookdetail',
      async: true,
      autoWidth: true,
      draggable: false,
      focusOpener: true,
      overrideAlt: false,
      focusImage: false,
      noAnimationMobile: true,
      noAnimation: true,
      onZoom: this.onZoom,
      ariaLabel: ariaLabelToSet,
      className: 'look-zoom-img-wrapper'
    }
    const zoom = new Zoom(zoomableImages, options)
    zoom.create()
    zoomableImages.forEach(imgZoom => {
      this.zoomArray.push(imgZoom)
    })
    if (this.btnZoom !== null) {
      this.btnZoom.addEventListener('click', this.zoomCurrentImage)
    }
    if (this.btnZoom !== null) {
      this.showHideButtonZoom()
    }
  }

  showHideButtonZoom = () => {
    if (!this.lookPageWithProducts) {
      this.btnZoom ? this.btnZoom.classList.remove('is-hidden') : ''
      if (isMobileScreen()) {
        this.pageControlsWrapper.closest('.look-page__controls').style.display = 'block'
        select('.js-page-control', this.pageControlsWrapper).classList.add('is-hidden')
        select('.back-to-looks span').classList.add('is-sr-only')
      } else {
        select('.js-page-control', this.pageControlsWrapper).classList.remove('is-hidden')
        select('.back-to-looks span').classList.remove('is-sr-only')
      }
    }
  }

  onZoom = () => {
    document.DLKit.push('when-user-zoom-look-image', {
      eventAction: 'zoom',
      eventCategory: 'pdp look'
    })
  }

  zoomCurrentImage = () => {
    if (this.indexControlImage >= 0) {
      const id = `look-details-${this.indexControlImage}`
      this.zoomArray.forEach(btnImg => {
        if (id === btnImg.id) {
          btnImg.click()
        }
      })
    }
  }
}
